import { Asset } from 'expo-asset';
import * as Font from 'expo-font';
import colors from '../constants/colors';
import i18n from './i18n';
import { Image } from 'react-native';

export const getRandomColor = () => {
    return colors.randomColors[
        Math.floor(Math.random() * colors.randomColors.length)
    ];
};

export const isPng = (image) => {
    if (!image) return false;

    return image.includes('.png');
};

export const capitalize = (string) => {
    if (typeof string !== 'string') return '';
    return string.charAt(0).toUpperCase() + string.slice(1);
};

export const calculateTimePassed = (time) => {
    if (!time) {
        return null;
    }

    const currentDateTIme = new Date();
    const checkTime = new Date(time);

    const yearDifference =
        currentDateTIme.getFullYear() - checkTime.getFullYear();
    const monthDifference = currentDateTIme.getMonth() - checkTime.getMonth();
    const dayDifference = currentDateTIme.getDate() - checkTime.getDate();
    const hourDifference = currentDateTIme.getHours() - checkTime.getHours();
    const minuteDifference =
        currentDateTIme.getMinutes() - checkTime.getMinutes();
    const secondDifference =
        currentDateTIme.getSeconds() - checkTime.getSeconds();

    if (yearDifference > 0) {
        return `${yearDifference} ${
            yearDifference > 1 ? i18n.t('years') : i18n.t('year')
        }`;
    }

    if (monthDifference > 0) {
        return `${monthDifference} ${
            monthDifference > 1 ? i18n.t('months') : i18n.t('month')
        }`;
    }

    if (dayDifference > 0) {
        return `${dayDifference} ${
            dayDifference > 1 ? i18n.t('days') : i18n.t('day')
        }`;
    }

    if (hourDifference > 0) {
        return `${hourDifference} ${
            hourDifference > 1 ? i18n.t('hours') : i18n.t('hour')
        }`;
    }

    if (minuteDifference > 0) {
        return `${minuteDifference} ${
            minuteDifference > 1 ? i18n.t('minutes') : i18n.t('minute')
        }`;
    }

    return `${secondDifference} ${
        secondDifference > 1 ? i18n.t('seconds') : i18n.t('second')
    }`;
};

export const getGridArray = (array, elementsInRow) => {
    const grid = [];
    for (let i = 0; i < Math.ceil(array.length / elementsInRow); i += 1) {
        grid[i] = [];
        for (
            let j = 0;
            j < elementsInRow && j + i * elementsInRow < array.length;
            j += 1
        ) {
            grid[i][j] = array[j + i * elementsInRow];
        }
    }
    return grid;
};

export const cacheImages = (images) => {
    return images.map((image) => {
        if (typeof image === 'string') {
            return Image.prefetch(image);
        }
        return Asset.fromModule(image).downloadAsync();
    });
};

export const checkIfLiked = (Wishlists, productId) => {
    let likedProducts = [];
    Wishlists.forEach((wishlist) => {
        if (!wishlist.products || !wishlist.products.length) return;
        likedProducts = likedProducts.concat(wishlist.products);
    });

    if (!productId || !likedProducts.length) return;
    const inLikedProducts = likedProducts.find(
        (product) => productId === product,
    );

    return !!inLikedProducts;
};

export const generateItemLabel = (item) => {
    let name = item.name;

    if (item.name && item.name.length > 20) {
        name = `${item.name.slice(0, 20)}...`;
    }

    return `${item.brand} - ${name}`;
};

export const cacheFonts = (fonts) => {
    return fonts.map((font) => Font.loadAsync(font));
};

export const getRatingColor = (rating) => {
    // 10 - 8.75 green
    // 8.75 - 7.5 light green
    // 6.5 - 7.5 yellow
    // 5 - 6.5 // dark yellow
    // 5 red

    if (rating < 5) return colors.darkRed;
    if (rating < 6.5) return colors.orange;
    if (rating < 7.5) return colors.yellow;
    if (rating < 8.75) return colors.lightGreen;

    return colors.green;
};

export const generateKey = (category) =>
    category.replace(' ', '-').toLowerCase();
