import React from 'react';
import {View} from "react-native";
import colors from "../../../constants/colors";

const Spacer = ({h, v, f, l, style}) => {
    const line = l ? {borderBottomWidth: 1, borderColor: colors.lighterGray} : {};
    return (
        <View style={[{paddingLeft:h, paddingTop: v, flex: f}, line, style]}/>
    )
};

export default Spacer;