import React, { PureComponent } from 'react';
import { FlatList, View, Text } from 'react-native';
import Content from './Content';
import i18n from '../../../../utils/i18n';
import Review from './Review';
import styles from './styles/ProductMediaContent.style';
import TagsMenu from '../TagsMenu';

class ProductMediaContent extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      tags: [],
      selectedTags: []
    };
  }

  componentDidMount() {
    this.getItemTags();
  }

  componentDidUpdate(prevProps, prevState) {
    const { product, onSelectedTagsChange, category } = this.props;
    const { selectedTags } = this.state;
    const { product: oldProduct, category: oldCategory } = prevProps;
    const { selectedTags: oldSelectedTags } = prevState;

    if (product._id !== oldProduct._id) {
      this.getItemTags();
    }

    if (selectedTags.length !== oldSelectedTags.length) {
      onSelectedTagsChange(selectedTags);
    }

    if (category !== oldCategory && this.mainFlatList) {
      this.mainFlatList.scrollToOffset({ x: 0, y: 0, animated: true });
    }
  }

  selectTag = tag => {
    const { selectedTags } = this.state;
    const selected = selectedTags.find(t => tag.value === t);

    if (selected) {
      return this.setState({
        selectedTags: selectedTags.filter(tag1 => tag1 !== tag.value)
      });
    }

    return this.setState({ selectedTags: [...selectedTags, tag.value] });
  };

  getItemTags = () => {
    const { product } = this.props;
    this.setState({ tags: [5, 4, 3, 2, 1, ...product.reviews_tags] });
  };

  renderItem = ({ item, index }) => {
    const { category } = this.props;

    switch (category) {
      case 'comments':
        return <Review item={item} selectedTags={this.getTagsToHighlight()} />;

      default:
        return <Content item={item} index={index} />;
    }
  };

  getTagsToHighlight = () => {
    const { selectedTags } = this.state;
    return selectedTags.reduce((tags, tag) => {
      if (typeof tag === 'string') tags.push(tag);

      return tags;
    }, []);
  };

  convertTagsToObject = tags => {
    return tags.map(tag => {
      const objTag = { name: tag, value: tag };
      if (typeof tag === 'number') {
        objTag.icon = {
          name: 'star',
          style: {
            color: '#ffd700',
            fontSize: 18,
            marginLeft: 5
          },
          type: 'FontAwesome'
          // fontSize: 16
        };
      }

      return objTag;
    });
  };

  renderHeader = () => {
    const { tags, selectedTags } = this.state;
    const mediaTags = this.convertTagsToObject(tags);
    const sTags = this.convertTagsToObject(selectedTags);

    return (
      <TagsMenu
        tags={mediaTags}
        selectedTags={sTags}
        onItemPress={this.selectTag}
        selectedItemStyle={styles.tagStyle}
        unselectedItemStyle={styles.tagStyle}
        style={styles.headerContainer}
      />
    );
    // return (
    //   <View id="test" style={styles.headerContainer}>
    //     <FlatList
    //       style={styles.flatList}
    //       data={tags}
    //       extraData={extraData}
    //       renderItem={this.renderTag}
    //       keyExtractor={this.tagKeyExtractor}
    //       horizontal
    //       showsHorizontalScrollIndicator={false}
    //     />
    //   </View>
    // );
  };

  keyExtractor = (item, index) => `${item._id}-${index}`;

  setMainFlatListRef = ref => {
    this.mainFlatList = ref;
  };

  render() {
    const { content, refreshing, onRefresh, refreshControl, category } = this.props;
    const extraData = { content, category };

    return (
      <>
        {category === 'comments' ? this.renderHeader() : null}
        <FlatList
          ref={this.setMainFlatListRef}
          data={content}
          extraData={extraData}
          renderItem={this.renderItem}
          keyExtractor={this.keyExtractor}
          refreshing={refreshing}
          onRefresh={onRefresh}
          refreshControl={refreshControl}
          // ListHeaderComponent={this.renderHeader}
          ListEmptyComponent={
            <View style={styles.emptyContainer}>
              {!refreshing ? (
                <Text style={styles.emptyText}>{i18n.t('product.noContent')}</Text>
              ) : null}
            </View>
          }
        />
      </>
    );
  }
}

export default ProductMediaContent;
