import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { StyleSheet, Text, View, Platform } from 'react-native';
import i18n from '../utils/i18n';
import fontSize from '../constants/fontSize';
import colors from '../constants/colors';
import StickyHeaderView from '../components/app/views/StickyHeaderView';
import MainHeader from '../components/app/content/MainHeader';
import SearchBar from '../components/search/SearchBar';
import { initialParams, search } from '../store/search';
import spacing from '../constants/spacing';
import ContentCard from '../components/search/ContentCard';
import ListAddFlow from '../components/lists/ListAddFlow';
import { addItemList, removeItemList } from '../store/lists';

const HomeScreen = () => {
    const dispatch = useDispatch();
    const [state, setState] = useState({ visible: false });
    const data = useSelector((state) => state.HomeSearch);
    const Meta = useSelector((state) => state.Misc);
    const lists = useSelector((state) => state.Lists);
    let componentIsRendered = false;
    let reviewsTag = [];
    try {
        reviewsTag = Meta.skincare.labels.filter((x) => x._id === 'reviews');
    } catch (e) {}
    const allLists = lists ? Object.values(lists) : [];
    const products = allLists.filter((x) => x.type === 'products');

    const onProductLike = (item) => {
        setState({ ...state, visible: true, item: item, type: 'products' });
    };

    const addToList = (listId, item) => {
        dispatch(addItemList(listId, item));
    };

    const removeFromList = (listId, item) => {
        dispatch(removeItemList(listId, item));
    };

    useEffect(() => {
        dispatch(
            search({
                params: { ...initialParams, type: 'all' },
                view: '_home',
            }),
        );

        // RED SQUARE WORKAROUND
        setTimeout(() => {
            if (Platform.OS === 'web') {
                const brokenActionSheetOnWeb = document.querySelector(
                    '#root > div > div > div > div > div:nth-child(2)',
                );
                if (brokenActionSheetOnWeb) {
                    brokenActionSheetOnWeb.style.display = 'none';
                    componentIsRendered = true;
                }
            }
        }, 0);
    }, []);

    const loadMore = () => {
        if (!data.last.isLast && !data.loading) {
            dispatch(
                search({
                    params: {
                        ...data.last,
                        page: data.last.page + 1,
                    },
                    view: '_home',
                }),
            );
        }
    };

    const empty = (
        <View style={styles.listEmptyMessage}>
            <Text style={{ ...styles.listEmptyMessage, ...styles.text }}>
                {i18n.t('emptyMedia')}
            </Text>
        </View>
    );

    const renderCard = ({ item }) => {
        return <ContentCard list={item} onProductLike={onProductLike} />;
    };

    return (
        <StickyHeaderView
            data={data.last.items}
            empty={empty}
            loadMore={loadMore}
            loading={data.loading}
            renderItem={renderCard}
            resetScroll={data.last.time}
            page={data.last.page}
            isHomeView>
            <MainHeader hideSymbol>
                <SearchBar data={data} view={'_home'} />
                {state.visible ? (
                    <ListAddFlow
                        visible={state.visible}
                        item={state.item}
                        type={state.type}
                        onClose={() => setState({ visible: false })}
                        onAdd={addToList}
                        onDelete={removeFromList}
                    />
                ) : null}
            </MainHeader>
        </StickyHeaderView>
    );
};

HomeScreen.navigationOptions = {
    headerTransparent: true,
    title: null,
};

export default HomeScreen;

const styles = StyleSheet.create({
    refreshContainer: {
        flex: 1,
        alignItems: 'center',
        justifyContent: 'center',
        backgroundColor: 'white',
    },
    listEmptyMessage: {
        flex: 1,
        alignItems: 'center',
        backgroundColor: 'white',
        paddingTop: 10,
    },
    text: {
        fontSize: fontSize.medium,
        color: colors.darkGray,
    },
    authorCard: {
        backgroundColor: 'white',
        paddingTop: spacing.big,
        paddingLeft: spacing.big,
        paddingRight: spacing.big,
        marginTop: 0,
        marginBottom: 4,
        marginLeft: 0,
        marginRight: 0,
        paddingBottom: spacing.big,
    },
    productCard: {
        backgroundColor: 'white',
        paddingTop: spacing.big,
        paddingLeft: spacing.medium,
        paddingRight: spacing.medium,
        marginTop: 0,
        marginBottom: 4,
        marginLeft: 0,
        marginRight: 0,
        paddingBottom: spacing.big,
    },
});
