import React, {PureComponent} from 'react';
import {FlatList, StyleSheet, Text, TouchableOpacity, View} from 'react-native';
import {Icon} from 'native-base';
import TextButton from '../../app/buttons/TextButton';
import colors from "../../../constants/colors";
import spacing from "../../../constants/spacing";
import fontSize from "../../../constants/fontSize";

class TagsMenu extends PureComponent {
    onPress = item => {
        const {onItemPress} = this.props;
        if (onItemPress) onItemPress(item);
    };

    renderItem = ({item}) => {
        const {
            selectedTags,
            selectedItemStyle,
            unselectedItemStyle,
            selectedTextStyle,
            unselectedTextStyle
        } = this.props;
        // !! to force into boolean
        const isSelected = !!selectedTags.find(tag => {
            if (!tag.value && !item.value) {
                return tag === item;
            }
            return tag.value === item.value;
        });

        if (item.icon) {
            return (
                <TouchableOpacity onPress={() => this.onPress(item)}>
                    <View
                        style={
                            isSelected
                                ? [styles.selectedIconItemStyle, selectedItemStyle]
                                : [styles.unselectedIconItemStyle, unselectedItemStyle]
                        }
                    >
                        <Text
                            style={
                                isSelected
                                    ? [styles.selectedTextStyle, selectedTextStyle]
                                    : [styles.unselectedTextStyle, unselectedTextStyle]
                            }
                        >
                            {item.name}
                        </Text>
                        <Icon {...item.icon} />
                    </View>
                </TouchableOpacity>
            );
        }

        return (
            <TextButton
                text={item.name || item}
                style={
                    isSelected
                        ? [styles.selectedItemStyle, selectedItemStyle]
                        : [styles.unselectedItemStyle, unselectedItemStyle]
                }
                textStyle={
                    isSelected
                        ? [styles.selectedTextStyle, selectedTextStyle]
                        : [styles.unselectedTextStyle, unselectedTextStyle]
                }
                onPress={() => this.onPress(item)}
            />
        );
    };

    render() {
        const {tags, selectedTags, style} = this.props;
        return (
            <View style={[styles.container, style]}>
                <FlatList
                    contentContainerStyle={styles.listItemsContainer}
                    keyExtractor={item => `${item.value || item}-tag`}
                    horizontal
                    showsHorizontalScrollIndicator={false}
                    data={tags}
                    extraData={selectedTags}
                    renderItem={this.renderItem}
                />
            </View>
        );
    }
}

const styles = StyleSheet.create({
    container: {
        height: 40,
        // alignItems: 'center',
        justifyContent: 'center',
        backgroundColor: colors.white
    },
    listItemsContainer: {
        alignItems: 'center'
    },
    unselectedItemStyle: {
        minWidth: 60,
        borderRadius: 20,
        paddingLeft: spacing.medium,
        paddingRight: spacing.medium,
        paddingTop: spacing.small,
        paddingBottom: spacing.small
    },
    unselectedIconItemStyle: {
        minWidth: 60,
        borderRadius: 20,
        paddingLeft: spacing.medium,
        paddingRight: spacing.medium,
        paddingTop: spacing.small,
        paddingBottom: spacing.small,
        flexDirection: 'row'
    },
    selectedItemStyle: {
        minWidth: 60,
        borderRadius: 20,
        paddingLeft: spacing.medium,
        paddingRight: spacing.medium,
        paddingTop: spacing.small,
        paddingBottom: spacing.small,
        backgroundColor: colors.lighterGray
    },
    selectedIconItemStyle: {
        minWidth: 60,
        borderRadius: 20,
        paddingLeft: spacing.medium,
        paddingRight: spacing.medium,
        paddingTop: spacing.small,
        paddingBottom: spacing.small,
        backgroundColor: colors.lighterGray,
        flexDirection: 'row'
    },
    unselectedTextStyle: {
        color: colors.categoriesLightGray,
        fontFamily: 'OpenSansSemiBold',
        fontSize: fontSize.semiMedium,
        fontWeight: '500'
    },
    selectedTextStyle: {
        fontFamily: 'OpenSansBold',
        fontSize: fontSize.semiMedium,
        color: colors.categoriesGray,
        fontWeight: 'bold'
    }
});


export default TagsMenu;
