import React from 'react';
import { View, Text, StyleSheet } from 'react-native';
import { getRatingColor } from '../../../utils/functions';
import fontSize from '../../../constants/fontSize';

// Score component showing product score in percentage
// 0-33 -> red , 33-66 -> yellow, 66-100 -> green color

const styles = StyleSheet.create({
  container: {
    width: '15%'
  },
  score: {
    fontSize: fontSize.extraSmall,
    fontFamily: 'OpenSans',
  }
});

const Score = ({ percent, radius, style, scoreStyle }) => {
  const color = getRatingColor(percent.toFixed(1));
  return (
    <View style={[styles.container, style]}>
      {/*<PercentageCircle*/}
      {/*  radius={radius}*/}
      {/*  percent={percent * 10}*/}
      {/*  color={color}*/}
      {/*  bgcolor={colors.white}*/}
      {/*  borderWidth={1}*/}
      {/*>*/}
        <Text style={[styles.score, { color }, scoreStyle]}>{percent}</Text>
      {/*</PercentageCircle>*/}
    </View>
  );
};

export default Score;
