import React from 'react';
import { Platform, Dimensions } from 'react-native';
import { createAppContainer, createSwitchNavigator } from 'react-navigation';
import { createStackNavigator } from 'react-navigation-stack';
import { createBottomTabNavigator } from 'react-navigation-tabs';
import CustomTabBar from './components/app/other/CustomTabBar';
import ProductDetailsScreen from './components/screens/ProductDetailsScreen';
import SplashScreen from './screens/SplashScreen';
import LoginScreen from './screens/LoginScreen';
import defaultNavigationOptions from './components/app/other/defaultNavigationOptions';
import WebViewModal from './components/app/views/WebViewModal';
import AuthorScreen from './screens/AuthorScreen';
import ListsScreen from './screens/ListsScreen';
import SearchScreen from './screens/SearchScreen';
import HomeScreen from './screens/HomeScreen';

const HomeStack = createStackNavigator(
    {
        HomeMain: HomeScreen,
        Author: AuthorScreen,
        ProductDetails: ProductDetailsScreen,
    },
    {
        defaultNavigationOptions,
    },
);

const SearchStack = createStackNavigator(
    {
        SearchMain: SearchScreen,
        Author: AuthorScreen,
        ProductDetails: ProductDetailsScreen,
    },
    {
        defaultNavigationOptions,
    },
);

const ListStack = createStackNavigator(
    {
        ListsMain: ListsScreen,
        Author: AuthorScreen,
        ProductDetails: ProductDetailsScreen,
    },
    {
        defaultNavigationOptions,
    },
);

const TabNavigation = createBottomTabNavigator(
    {
        Home: HomeStack,
        Search: SearchStack,
        Lists: ListStack,
        // Profile: ProfileStack
    },
    {
        tabBarComponent: () => <CustomTabBar />,
        navigationOptions: {
            headerShown: false,
            headerBackTitle: null,
        },
    },
);

let RootStack;
if (Platform.OS === 'web' && Dimensions.get('window').width > 500) {
    RootStack = createStackNavigator(
        {
            Home: HomeStack,
            Search: SearchStack,
            Lists: ListStack,
            Auth: LoginScreen,
            WebViewModal,
        },
        {
            defaultNavigationOptions,
        },
    );
} else {
    RootStack = createStackNavigator(
        {
            Root: TabNavigation,
            Auth: LoginScreen,
            WebViewModal,
        },
        {
            // mode: 'modal',
            defaultNavigationOptions,
        },
    );
}

const AppStack = createSwitchNavigator(
    {
        Root: RootStack,
        Splash: SplashScreen,
    },
    {
        initialRouteName: 'Splash',
    },
);

export default createAppContainer(AppStack);
