import React, {useState} from 'react';
import {View} from 'react-native';
import {useDispatch, useSelector} from 'react-redux'
import {withNavigation} from 'react-navigation';
import ActionRedButton from "../app/buttons/ActionRedButton";
import {addItemList, createList, removeItemList} from "../../store/lists";
import Spacer from "../app/other/Spacer";
import ListAddFlow from "../lists/ListAddFlow";
import IconButton from "../app/buttons/IconButton";
import colors from "../../constants/colors";

const AuthorActionBox = ({navigation, author, hideAddList, addModal, listId, hideDelete}) => {
        const dispatch = useDispatch();
        const user = useSelector(state => state.user);
        const lists = useSelector(state => state.Lists);
        const [state, setState] = useState({visible: false});

        // check if following and author in list
        const allLists = user.isLoggedIn && lists ? Object.values(lists) : null
        const following = allLists ? allLists.find(x => x.type === 'following') : null;
        const others = allLists ? allLists.filter(x => x.type === 'influencers') : null;
        const sizeInLists = others ? others.filter(x => x.items.find(x => x._id === author._id)).length : 0;
        const inList = following && following.items.find(x => x._id === author._id);

        const follow = async () => {
            if (!user.isLoggedIn) {
                navigation.navigate('Auth');
            } else {
                if (following && !inList) {
                    addToList(following.id, author)
                } else if (following && inList) {
                    removeFromList(following.id, author)
                } else if (!following) {
                    const list = {
                        isPublic: true,
                        description: 'This is your main list of influencers',
                        name: 'Following',
                        type: 'following',
                    };
                    dispatch(createList(user.uid, author, list))
                }
            }
        };

        const addToList = (listId, author) => {
            dispatch(addItemList(listId, author))
        };

        const removeFromList = (listId, author) => {
            dispatch(removeItemList(listId, author))
        };

        return (
            <View style={{flexDirection: 'row', alignItems: 'center'}}>
                {!addModal ? null :
                    <ListAddFlow visible={state.visible}
                                 item={state.item}
                                 type={state.type}
                                 onClose={() => setState({visible: false})}
                                 onAdd={addToList}
                                 onDelete={removeFromList}
                    />
                }
                <ActionRedButton text={inList ? 'Following' : 'Follow'} isSelected={inList} onPress={follow}/>

                {hideDelete ? null :
                    <>
                        <Spacer h={4}/>
                        <IconButton iconFont={'MaterialCommunityIcons'}
                                    iconSize={20}
                                    iconColor={colors.darkGray}
                                    onPress={() => removeFromList(listId, author)}
                                    iconName={'delete-outline'}
                        />
                    </>
                }
                {!hideAddList ?
                    <>
                        <Spacer h={8}/>
                        <ActionRedButton text='Add to list' isSelected={sizeInLists > 0} onPress={() => {
                            setState({...state, visible: true, item: author, type: 'influencers'})
                        }}/>
                    </>
                    : null}
            </View>
        );
    }
;

export default withNavigation(AuthorActionBox);