import React from 'react';
import {Text} from 'react-native';
import colors from '../../constants/colors';
import fontSize from '../../constants/fontSize';
import Styles from "../app/other/Styles";
import StarRating from '../app/buttons/StarRaitingButtons';
import {withNavigation} from "react-navigation";
import SearchCard from "../search/SearchCard";

const ProductCard = ({navigation, product, caption, seeMedia, inList, onLike}) => {

    const onProductClicked = ({_id}) => {
        navigation.navigate('ProductDetails', {productId: product._id});
    };

    return (
        <SearchCard
            image={product.img}
            supperTitle={product.brand}
            title={product.name}
            caption={`See related media${caption.extra}`}
            inList={inList}
            onLike={() => {
                onLike(product)
            }}
            onPress={onProductClicked}
            seeMedia={() => {
                seeMedia(product)
            }}
        >
            <Text style={[Styles.text, {fontSize: fontSize.small, color: colors.gray, paddingTop: 3}]}>
                Mentioned in {caption.full}
            </Text>
            <StarRating
                disabled
                maxStars={5}
                rating={product.score / 2} // todo fix remove one products updated
                starSize={13}
                fullStarColor={colors.yellow}
                containerStyle={{justifyContent: 'flex-start', paddingTop: 10, paddingBottom: 10}}
                starStyle={{paddingRight: 3}}
            />
        </SearchCard>
    );
};
export default withNavigation(ProductCard);
