import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import {
    Keyboard,
    KeyboardAvoidingView,
    Platform,
    Text,
    TextInput,
    View,
    ScrollView,
} from 'react-native';

import RenderModal from '../../app/views/RenderModal';
import { Toast } from 'native-base';
import TextButton from '../../app/buttons/TextButton';
import { sendFeedback } from '../../../store/actions/user_actions';
import i18n from '../../../utils/i18n';
import styles from './styles/FeedbackModal.style';
import SafeArea from '../../app/content/SafeArea';

class FeedbackModal extends PureComponent {
    constructor(props) {
        super(props);

        this.state = {
            message: '',
            email: '',
        };
    }

    onChange = (message) => {
        this.setState({ message });
    };

    onChangeEmail = (email) => {
        this.setState({ email });
    };

    send = () => {
        const { message } = this.state;
        const { hideModal, user } = this.props;
        const payload = {
            email: user.email,
            uid: user.uid,
            message,
            api_token: 'NqWKEbbhvDHT70tkczfmYFkl8gRmakWGm3Yn',
        };
        Keyboard.dismiss();

        sendFeedback(payload)
            .then(this.showThankYouAlert)
            .catch(this.showError)
            .finally(() => {
                this.setState({ message: '', email: '' });
                hideModal();
            });
    };

    showThankYouAlert = () => {
        setTimeout(() => {
            Toast.show({
                text: i18n.t('feedbackSuccessMessage'),
            });
        }, 50);
    };

    showError = () => {
        setTimeout(() => {
            Toast.show({
                text: i18n.t('feedbackError'),
            });
            this.refs.toast.show('hello world!—É–∫—â–∫');
        }, 50);
    };

    render() {
        const { visible, hideModal, user } = this.props;
        const { message, email } = this.state;
        const opt = {
            visible: visible,
            onBackdropPress: hideModal,
            onBackButtonPress: hideModal,
            style: styles.modal,
        };
        return (
            <RenderModal options={opt}>
                <KeyboardAvoidingView
                    behavior={Platform.OS === 'ios' ? 'padding' : null}
                    // keyboardVerticalOffset={Platform.OS === 'ios' ? 64 : 0}
                    keyboardShouldPersistTaps="always">
                    <SafeArea
                        style={[
                            styles.safeAreaStyle,
                            visible ? { paddingBottom: 0 } : {},
                        ]}>
                        <Toast ref="toast" />
                        <View style={styles.container}>
                            <View style={styles.header}>
                                <TextButton
                                    style={styles.cancelButton}
                                    text={i18n.t('cancel')}
                                    onPress={hideModal}
                                />

                                <Text style={styles.title}>
                                    {i18n.t('feedbackHeader')}
                                </Text>

                                <TextButton
                                    style={styles.resetButton}
                                    text={i18n.t('sendFeedback')}
                                    onPress={this.send}
                                />
                            </View>

                            <ScrollView style={styles.formScrollView}>
                                {!user.isLoggedIn ? (
                                    <TextInput
                                        style={styles.textArea}
                                        value={email}
                                        onChangeText={this.onChangeEmail}
                                        keyboardType="email-address"
                                        placeholder={i18n.t('auth.email')}
                                    />
                                ) : null}
                                <TextInput
                                    style={[styles.textArea, { height: 100 }]}
                                    value={message}
                                    onChangeText={this.onChange}
                                    numberOfLines={5}
                                    placeholder={i18n.t('feedbackQuestion')}
                                    multiline
                                />
                            </ScrollView>
                        </View>
                    </SafeArea>
                </KeyboardAvoidingView>
            </RenderModal>
        );
    }
}

export default connect((state) => {
    return { user: state.user };
})(FeedbackModal);
