import React from 'react';
import {StyleSheet, Text} from 'react-native';
import colors from '../../constants/colors';
import fontSize from '../../constants/fontSize';
import Styles from "../app/other/Styles";
import StarRating from '../../components/app/buttons/StarRaitingButtons';
import {withNavigation} from "react-navigation";
import SearchCard from "../search/SearchCard";
import AuthorCard from "./AuthorCard";
import spacing from "../../constants/spacing";

const AuthorCardSearch = ({navigation, author, caption, inList, onLike}) => {

    const gotoAuthor = ({_id}) => {
        navigation.navigate('Author', {author: author, filter: true, fromRoute: navigation.state.routeName});
    };

    return (
        <SearchCard
            image={author.image}
            title={author.name}
            caption={`See their media${caption.extra}`}
            inList={inList}
            onLike={() => {
                onLike(author)
            }}
            onPress={gotoAuthor}
            seeMedia={gotoAuthor}
            showYoutube={author.channelId}
            roundImage
        >
            <Text style={[Styles.text, {fontSize: fontSize.small, color: colors.gray}]}>
                {author.text}
                {author.text ? ', ' : ''}
                {caption.full}
            </Text>
            {!author.description ? null :
                <Text numberOfLines={2} style={styles.descriptionText}>{author.description}</Text>
            }
        </SearchCard>
    );
};

const styles = StyleSheet.create({
    descriptionText: {
        paddingTop: 5,
        paddingBottom: 5,
        fontSize: fontSize.semiMedium,
        fontFamily: 'OpenSans',
        color: colors.darkGray,
        lineHeight: 20,
    }
});

export default withNavigation(AuthorCardSearch);
