import { SIGN_IN_USER, SIGN_IN_ERROR, LOG_OUT_USER } from '../types';

const initialState = {
    isLoggedIn: false,
};

export default function (state = initialState, { type, payload }) {
    switch (type) {
        case SIGN_IN_USER:
            return {
                ...state,
                isLoggedIn: true,
                ...payload,
            };
        case LOG_OUT_USER:
            return { ...initialState };
        default:
            return state;
    }
}
