import React from 'react';
import { StyleSheet, Text, TouchableOpacity } from 'react-native';
import spacing from '../../../constants/spacing';
import colors from '../../../constants/colors';
import fontSize from '../../../constants/fontSize';

const SelectionButton = ({ isSelected, text, onPress }) => {
    return (
        <TouchableOpacity
            onPress={onPress}
            style={
                isSelected
                    ? [styles.container, styles.selectedItemStyle]
                    : [styles.container, styles.unselectedItemStyle]
            }>
            <Text
                style={
                    isSelected
                        ? [styles.text, styles.selectedTextStyle]
                        : [styles.text, styles.unselectedTextStyle]
                }>
                {text}
            </Text>
        </TouchableOpacity>
    );
};

const styles = StyleSheet.create({
    container: {
        justifyContent: 'center',
        borderRadius: 5,
        paddingLeft: spacing.medium,
        paddingRight: spacing.medium,
        paddingTop: spacing.small,
        paddingBottom: spacing.small,
    },
    text: {
        fontFamily: 'OpenSans',
        fontSize: fontSize.medium,
    },
    selectedItemStyle: {
        backgroundColor: colors.extraDarkGray,
    },
    unselectedItemStyle: {
        borderWidth: 0.5,
        borderColor: colors.darkGray,
    },
    selectedTextStyle: {
        color: colors.white,
    },
    unselectedTextStyle: {
        color: colors.darkGray,
    },
});

export default SelectionButton;
