import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import LoginForm from '../components/authentication/LoginForm';
import { SIGN_IN_USER } from '../store/types';
import firebase from '../utils/firebase';
import { Toast } from 'native-base';
import i18n from '../utils/i18n';
import { View, Button, Platform } from 'react-native';
import * as Facebook from 'expo-facebook';
import * as Google from 'expo-google-app-auth';
import { withNavigation } from 'react-navigation';
import { getUserLists } from '../store/lists';
import Styles from '../components/app/other/Styles';
const LoginScreen = ({ navigation }) => {
    const dispatch = useDispatch();

    const [state, setState] = useState({ visible: false });

    const process = (response) => {
        const data = { email: response.user.email, uid: response.user.uid };
        dispatch({ type: SIGN_IN_USER, payload: data });

        // download user data
        dispatch(getUserLists(data.uid));

        navigation.goBack();
    };

    const signin = async ({ email, password }) => {
        try {
            const response = await firebase
                .auth()
                .signInWithEmailAndPassword(email, password);
            process(response);
        } catch (err) {
            Toast.show({
                text: i18n.t('auth.loginFailed', {
                    error: err.message || err || '',
                }),
                type: 'danger',
                duration: 5000,
            });
        }
    };

    const signup = async ({ email, password }) => {
        try {
            const response = await firebase
                .auth()
                .createUserWithEmailAndPassword(email, password);
            process(response);
        } catch (err) {
            Toast.show({
                text: i18n.t('auth.registrationFailed', {
                    error: err.message || err || '',
                }),
                type: 'danger',
                duration: 5000,
            });
        }
    };

    const facebook = async () => {
        if (Platform.OS === 'web') {
            const provider = new firebase.auth.FacebookAuthProvider();
            // provider.addScope('profile');
            // provider.addScope('email');
            firebase
                .auth()
                .signInWithPopup(provider)
                .then(function (result) {
                    process(result);
                })
                .catch(function (error) {
                    var errorCode = error.code;
                    var errorMessage = error.message;
                    var email = error.email;
                    var credential = error.credential;
                    alert(
                        "We can't auth you through Facebook:\n\n" +
                            'ERROR CODE:\n' +
                            errorCode +
                            '\n\n' +
                            'ERROR MESSAGE:\n' +
                            errorMessage,
                    );
                });
        } else {
            try {
                const appId = Expo.Constants.manifest.extra.facebook.appId;
                const permissions = ['public_profile', 'email'];
                await Facebook.initializeAsync(appId);
                const response = await Facebook.logInWithReadPermissionsAsync(
                    appId,
                    { permissions },
                ); // Permissions required, consult Facebook docs
                const { type, token } = response;
                switch (type) {
                    case 'success': {
                        await firebase
                            .auth()
                            .setPersistence(
                                firebase.auth.Auth.Persistence.LOCAL,
                            ); // Set persistent auth state
                        const credential = firebase.auth.FacebookAuthProvider.credential(
                            token,
                        );
                        const facebookProfileData = await firebase
                            .auth()
                            .signInWithCredential(credential); //signInAndRetrieveDataWithCredential(credential);  // Sign in with Facebook credential
                        process(facebookProfileData);
                        break;
                    }
                    case 'cancel': {
                        throw new Error('');
                    }
                }
            } catch (err) {
                Toast.show({
                    text: i18n.t('auth.registrationFailed', {
                        error: err.message || err || '',
                    }),
                    type: 'danger',
                    duration: 5000,
                });
            }
        }
    };

    const google = async () => {
        if (Platform.OS === 'web') {
            const provider = new firebase.auth.GoogleAuthProvider();
            provider.addScope('profile');
            provider.addScope('email');
            await firebase
                .auth()
                .signInWithPopup(provider)
                .then(function (result) {
                    process(result);
                })
                .catch(function (error) {
                    var errorCode = error.code;
                    var errorMessage = error.message;
                    var email = error.email;
                    var credential = error.credential;
                    alert("We can't auth you through Google: " + errorMessage);
                });
        } else {
            try {
                const configuration = {
                    iosClientId:
                        Expo.Constants.manifest.extra.google.iosClientId,
                    androidClientId:
                        Expo.Constants.manifest.extra.google.androidClientId,
                    iosStandaloneAppClientId:
                        Expo.Constants.manifest.extra.google
                            .iosStandaloneAppClientId,
                    androidStandaloneAppClientId:
                        Expo.Constants.manifest.extra.google
                            .androidStandaloneAppClientId,
                    scopes: ['profile', 'email'],
                };
                const { type, accessToken, user } = await Google.logInAsync(
                    configuration,
                );

                if ('success' === type) {
                    let userInfoResponse = await fetch(
                        'https://www.googleapis.com/userinfo/v2/me',
                        {
                            headers: { Authorization: `Bearer ${accessToken}` },
                        },
                    );
                    console.dir(userInfoResponse);

                    await firebase
                        .auth()
                        .setPersistence(firebase.auth.Auth.Persistence.LOCAL); // Set persistent auth state
                    const credential = firebase.auth.GoogleAuthProvider.credential(
                        null,
                        accessToken,
                    );
                    const googleProfileData = await firebase
                        .auth()
                        .signInWithCredential(credential); //signInAndRetrieveDataWithCredential(credential);  // Sign in with Google credential

                    process(googleProfileData);
                }
            } catch (err) {
                Toast.show({
                    text: i18n.t('auth.registrationFailed', {
                        error: err.message || err || '',
                    }),
                    type: 'danger',
                    duration: 5000,
                });
            }
        }
    };

    return (
        <View style={Styles.container}>
            <View style={Styles.subContainer}>
                <LoginForm
                    SignIn={signin}
                    SignUp={signup}
                    Facebook={facebook}
                    Google={google}
                />
            </View>
        </View>
    );
};

LoginScreen.navigationOptions = {
    headerTransparent: true,
    title: null,
};

export default withNavigation(LoginScreen);
