import React, { PureComponent } from 'react';
import { Animated, Platform, StyleSheet, View, Text } from 'react-native';
import { SafeAreaView, withNavigation } from 'react-navigation';
import IconButton from '../buttons/IconButton';
import FeedbackModal from '../../screens/Feedback/FeedbackModal';
import colors from '../../../constants/colors';
import spacing from '../../../constants/spacing';
import fontSize from '../../../constants/fontSize';
import Constants from 'expo-constants/src/Constants';
import { NavigationActions } from 'react-navigation';

const TAB_BAR_OFFSET = -50;

class CustomTabBar extends PureComponent {
    constructor(props) {
        super(props);

        this.state = {
            offset: new Animated.Value(0),
            feedbackVisible: false,
        };
    }

    componentDidMount() {
        if (Platform.OS === 'android') {
            SafeAreaView.setStatusBarHeight(0);
        }
    }

    componentDidUpdate(prevProps) {
        // const {navigation: oldNavigation} = prevProps;
        // const {navigation} = this.props;
        // const {offset} = this.state;
        // const oldState = oldNavigation.state;
        // const oldRoute = oldState.routes[oldState.index];
        // const oldRouteRoute = oldRoute.routes[oldRoute.index];
        // const oldParams = oldRouteRoute.params;
        // const wasVisible = !oldParams || oldParams.visible;
        // const newState = navigation.state;
        // const newRoute = newState.routes[newState.index];
        // const newRouteRoute = newRoute.routes[newRoute.index];
        // const newParams = newRouteRoute.params;
        // const isVisible = !newParams || newParams.visible;
        // if (wasVisible && !isVisible) {
        //     Animated.timing(offset, {
        //         toValue: TAB_BAR_OFFSET,
        //         duration: 10
        //     }).start();
        // } else if (isVisible && !wasVisible) {
        //     Animated.timing(offset, {toValue: 0, duration: 10}).start();
        // }
    }

    navigate(screen, isCurrent, index) {
        const { navigation } = this.props;
        const params =
            isCurrent && index === 0
                ? { params: { scrollUp: Date.now() } }
                : {};
        const action = isCurrent
            ? {
                  action: NavigationActions.navigate({
                      routeName: screen + 'Main',
                      ...params,
                  }),
              }
            : {};
        navigation.navigate(
            NavigationActions.navigate({
                routeName: screen,
                ...action,
            }),
        );
    }

    // disableAlert = () => {
    //     const {timeout} = this.state;
    //     clearTimeout(timeout);
    //     this.setState({timeout: true});
    // };

    showFeedbackModal = () => {
        this.setState({ feedbackVisible: true });
    };

    hideFeedbackModal = () => {
        this.setState({ feedbackVisible: false });
    };

    getIcon = (screen) => {
        switch (screen) {
            case 'Home':
                return 'home';
            case 'Search':
                return 'search';
            case 'Lists':
                return 'heart';
            case 'FeedBack':
                return 'message-circle';
            default:
                return 'home';
        }
    };

    render() {
        const { navigation } = this.props;
        const navigationState = navigation.state;
        const { feedbackVisible, offset } = this.state;
        return (
            <View style={[styles.container]}>
                {navigationState.routes.map((route, i) => {
                    const isCurrent = navigationState.index === i;
                    const { routeName, index } = route;

                    return (
                        <View style={styles.tab} key={routeName}>
                            <IconButton
                                iconName={this.getIcon(routeName)}
                                iconSize={20}
                                iconColor={
                                    isCurrent ? colors.default : colors.semiGray
                                }
                                containerStyle={
                                    isCurrent
                                        ? [
                                              styles.unselectedTab,
                                              styles.selectedTab,
                                          ]
                                        : styles.unselectedTab
                                }
                                onPress={() =>
                                    this.navigate(routeName, isCurrent, index)
                                }
                            />
                        </View>
                    );
                })}
                <View style={styles.tab}>
                    <IconButton
                        iconName="message-circle"
                        iconSize={20}
                        iconColor={colors.semiGray}
                        onPress={this.showFeedbackModal}
                    />
                </View>

                <FeedbackModal
                    visible={feedbackVisible}
                    hideModal={this.hideFeedbackModal}
                    hideModalAlert={this.hideFeedbackModalAlert}
                />
            </View>
        );
    }
}

const styles = StyleSheet.create({
    container: {
        position: 'absolute',
        left: 0,
        right: 0,
        bottom: 0,
        flexDirection: 'row',
        // height: 50,
        padding: spacing.small,
        backgroundColor: colors.white,
        borderTopWidth: 1,
        borderColor: colors.lightGray,
        paddingBottom: Constants.statusBarHeight >= 44 ? 20 : 5,
    },
    tab: {
        flex: 1,
        height: 30,
        alignItems: 'center',
        justifyContent: 'center',
    },
    unselectedTab: {
        padding: 0,
        alignItems: 'center',
        justifyContent: 'center',
        height: '100%',
        width: 40,
    },
    selectedTab: {
        borderBottomWidth: 2,
        borderColor: colors.default,
    },
    toast: {
        borderRadius: 15,
        backgroundColor: colors.lightGray,
        borderWidth: 1,
        borderColor: colors.semiGray,
        color: colors.black,
        fontSize: fontSize.big,
        paddingTop: spacing.medium,
        paddingBottom: spacing.medium,
        fontWeight: 'bold',
        fontFamily: 'Roboto',
    },
});

export default withNavigation(CustomTabBar);
