import React from 'react';
import {Image, StyleSheet, Text, TouchableWithoutFeedback, View} from 'react-native';
import fontSize from '../../constants/fontSize';
import colors from '../../constants/colors';
import spacing from "../../constants/spacing";
import {withNavigation} from 'react-navigation';
import AuthorActionBox from "./AuthorActionBox";
import Icon from "../app/icons/Icon";
import Spacer from "../app/other/Spacer";

const AuthorCard = ({navigation, data, row, listId, children, hideActionBox, size = 1, showDescription, showMediaLink, caption, hideDelete, loadFilters, tags}) => {
    const s = StyleSheet.create({
        imageRow: {
            width: 50 * size,
            height: 50 * size,
            borderRadius: 30 * size,
        },
    });
    return (
        <TouchableWithoutFeedback onPress={() => navigation.navigate('Author', {author: data, filter: loadFilters, fromRoute: navigation.state.routeName, tags: tags})}>
            <View style={row ? [styles.containerRow, size > 1 ? {height: 150} : null] : styles.container}>

                <View style={[styles.imageContainer]}>
                    {data.image ?
                        <Image resizeMode="cover" style={row ? s.imageRow : styles.image} source={{uri: data.image}}/>
                        : <View style={row ? [s.imageRow, styles.noImageRow] : [styles.image, styles.noImage]}/>
                    }
                </View>

                <View style={row ? styles.nameFollowersContainerRow : styles.nameFollowersContainer}>
                    <View style={{flexDirection: 'row', alignItems: 'center'}}>
                        <Text numberOfLines={1} style={row ? styles.nameRow : styles.name}>{data.name}</Text>
                        <Spacer h={5}/>
                        {data.channelId && row ?
                            <Icon name='youtube' font='FontAwesome' size={fontSize.medium} color={colors.gray}/>
                            : null}
                    </View>

                    {data.text || children ? <Text
                        style={row ? styles.followersRow : styles.followers}>{data.text}{data.text && children ? ', ' : ''}{children}</Text> : null}

                    {showDescription && data.description ?
                        <Text numberOfLines={showMediaLink ? 2 : 3}
                              style={row ? styles.descriptionText : styles.descriptionText}>{data.description}</Text> : null}

                    {showMediaLink ?
                        <View style={{flexDirection: 'row', paddingTop: 20}}>
                            <Icon name='folder-video' font='Entypo' size={fontSize.medium} color={colors.gray}/>
                            <Spacer h={5}/>
                            <Text style={styles.followersRow}>See {data.count} media{caption}</Text>
                        </View> : <View style={{paddingTop: 5}}/>}
                </View>

                {!row || hideActionBox ? null :
                    <View style={styles.actionBox}>
                        <AuthorActionBox author={data} hideAddList listId={listId} hideDelete={hideDelete}/>
                    </View>}

            </View>
        </TouchableWithoutFeedback>
    )
};

export default withNavigation(AuthorCard);

const styles = StyleSheet.create({
    container: {
        flexDirection: 'column',
        alignItems: 'center',
        paddingRight: 10,
        paddingLeft: 10,
        paddingBottom: 5,
        paddingTop: 5,
    },
    containerRow: {
        flexDirection: 'row',
        alignItems: 'center',
        paddingTop: 5,
        paddingBottom: 5,
        backgroundColor: 'white',
        // height: 150,
    },
    actionBox: {
        alignItems: 'center',
        justifyContent: 'center',
        marginRight: 5
    },
    imageContainer: {
        alignItems: 'center',
        justifyContent: 'center',
    },
    noImage: {
        borderColor: colors.gray,
        borderWidth: 0.5,
    },
    image: {
        width: 100,
        height: 100,
        borderRadius: 50,
    },
    noImageRow: {
        borderColor: colors.gray,
        borderWidth: 0.5,
    },
    nameFollowersContainer: {
        alignItems: 'center',
    },
    nameFollowersContainerRow: {
        marginLeft: spacing.big,
        // justifyContent: 'flex-end',
        flex: 1
    },
    name: {
        color: colors.darkGray,
        fontWeight: 'bold',
        fontSize: fontSize.small,
        fontFamily: 'OpenSansBold',
        paddingTop: 5,
    },
    nameRow: {
        color: colors.darkGray,
        fontWeight: 'bold',
        fontSize: fontSize.big,
        fontFamily: 'OpenSansBold',
    },
    followers: {
        fontSize: fontSize.extraSmall,
        fontFamily: 'OpenSans',
        color: colors.gray
    },
    followersRow: {
        fontSize: fontSize.small,
        fontFamily: 'OpenSans',
        color: colors.gray
    },
    descriptionText: {
        paddingTop: 15,
        paddingBottom: 3,
        fontSize: fontSize.medium,
        fontFamily: 'OpenSans',
        color: colors.darkGray,
        lineHeight: 20,
    }
});
