import firebase, { db } from '../utils/firebase';

export function sortbyDate(a, b) {
    return b.modified - a.modified;
}

export const Lists = (state = null, { type, payload }) => {
    let out;
    switch (type) {
        case 'update_lists':
            return payload;
        case 'new_list':
            out = { ...state };
            out[payload.id] = payload;
            return out;
        case 'delete_list':
            out = { ...state };
            delete out[payload];
            return out;
        case 'add_to_list':
        case 'remove_from_list':
            const listId = payload.listId;
            const list = state[listId];
            list.modified = payload.modified;
            switch (type) {
                case 'add_to_list':
                    const items = [payload.item, ...list.items];
                    list.items = items;
                    break;
                case 'remove_from_list':
                    const wihtoutItems = list.items.filter(
                        (x) => x._id !== payload.item._id,
                    );
                    list.items = wihtoutItems;
                    break;
            }
            out = { ...state };
            out[listId] = list;
            return out;
        default:
            return state;
    }
};

async function getLists(dispatch, uid) {
    try {
        const ref = db.collection('lists');
        const query = ref.where('owner', '==', uid);
        let results = await query.get({ source: 'server' });

        let r = [];
        results.forEach((x) => (r[x.id] = { ...x.data(), id: x.id }));

        // reverse add of the items
        for (let key in r) {
            if (r[key].items) r[key].items = r[key].items.reverse();
        }

        // sort lists
        let out = {};
        const sortedList = Object.values(r).sort(sortbyDate);
        sortedList.forEach((x) => (out[x.id] = x));
        dispatch({ type: 'update_lists', payload: out });
    } catch (e) {
        console.log(e);
    }
}

export const getUserLists = (uid) => {
    return async (dispatch) => {
        getLists(dispatch, uid);
    };
};

export const createList = (owner, item, list) => {
    return (dispatch) => {
        try {
            const newList = {
                items: item ? [item] : [],
                owner: owner,
                isPublic: list.isPublic,
                description: list.description,
                name: list.name,
                type: list.type,
                modified: Date.now(),
            };
            db.collection('lists').doc().set(newList);
            getLists(dispatch, owner);
        } catch (e) {
            console.log(e.message);
        }
    };
};

export const updateList = (owner, list) => {
    return async (dispatch) => {
        try {
            const updates = {
                isPublic: list.isPublic,
                description: list.description,
                name: list.name,
                type: list.type,
                modified: Date.now(),
            };
            dispatch({ type: 'new_list', payload: { ...list, ...updates } });
            db.collection('lists').doc(list.id).update(updates);
        } catch (e) {
            console.log(e.message);
        }
    };
};

export const deleteList = (id) => {
    return async (dispatch) => {
        try {
            dispatch({ type: 'delete_list', payload: id });
            db.collection('lists').doc(id).delete();
        } catch (e) {
            console.log(e.message);
        }
    };
};

export const addItemList = (listId, item) => {
    return (dispatch) => {
        try {
            const ref = db.collection('lists').doc(listId);
            const modified = Date.now();
            ref.update({
                items: firebase.firestore.FieldValue.arrayUnion(item),
                modified: modified,
            });
            dispatch({
                type: 'add_to_list',
                payload: { item, listId, modified: modified },
            });
        } catch (e) {
            console.log(e.message);
        }
    };
};

export const removeItemList = (listId, item) => {
    return (dispatch) => {
        try {
            const ref = db.collection('lists').doc(listId);
            const modified = Date.now();
            ref.update({
                items: firebase.firestore.FieldValue.arrayRemove(item),
                modified: modified,
            });
            dispatch({
                type: 'remove_from_list',
                payload: { item, listId, modified: modified },
            });
        } catch (e) {
            console.log(e.message);
        }
    };
};
