import React from 'react';
import { StyleSheet, Text, TouchableOpacity, View } from 'react-native';
import spacing from '../../../constants/spacing';
import colors from '../../../constants/colors';
import fontSize from '../../../constants/fontSize';

const TabButton = ({ isSelected, text, onPress }) => {
    return (
        <TouchableOpacity
            onPress={onPress}
            style={
                isSelected
                    ? [styles.container, styles.selectedItemStyle]
                    : [styles.container, styles.unselectedItemStyle]
            }>
            <Text
                style={
                    isSelected
                        ? [styles.text, styles.selectedTextStyle]
                        : [styles.text, styles.unselectedTextStyle]
                }>
                {text}
            </Text>
        </TouchableOpacity>
    );
};

const styles = StyleSheet.create({
    container: {
        justifyContent: 'center',
        paddingLeft: spacing.medium,
        paddingRight: spacing.medium,
        paddingBottom: spacing.medium,
        marginRight: spacing.medium,
    },
    text: {
        fontFamily: 'OpenSans',
        fontSize: fontSize.medium,
        // paddingLeft: 5,
        // paddingRight: 5,
    },
    selectedItemStyle: {
        // backgroundColor: colors.lighterGray,
        borderBottomWidth: 1,
        // borderColor: colors.darkGray
    },
    unselectedItemStyle: {
        // backgroundColor: colors.red,
    },
    selectedTextStyle: {
        color: colors.extraDarkGray,
    },
    unselectedTextStyle: {
        color: colors.gray,
    },
});

export default TabButton;
