import {
    SIGN_IN_USER,
    LOG_OUT_USER,
} from '../types';
import firebase from '../../utils/firebase'
import ezencielAPI from '../../utils/ezapi';

export const checkSession = () => {
    return dispatch => {
        firebase.auth().onAuthStateChanged((user) => {
            if (user) {
                const data = {email: user.email, uid: user.uid}
                dispatch({type: SIGN_IN_USER, payload: data});
            } else {
                dispatch({type: LOG_OUT_USER});
            }
        });
    };
};

export const sendFeedback = data => ezencielAPI.post('/feedback', data);
