import React from 'react';
import {
    Image,
    StyleSheet,
    Text,
    TouchableWithoutFeedback,
    View,
} from 'react-native';
import colors from '../../constants/colors';
import fontSize from '../../constants/fontSize';
import spacing from '../../constants/spacing';
import StarRating from '../../components/app/buttons/StarRaitingButtons';
import { withNavigation } from 'react-navigation';
import IconButton from '../app/buttons/IconButton';

const ProductCardCarrousel = ({
    navigation,
    product,
    inList,
    onProductLike,
}) => {
    const onProductClicked = ({ _id }) => {
        navigation.navigate('ProductDetails', { productId: product._id });
    };

    return (
        <TouchableWithoutFeedback onPress={onProductClicked}>
            <View style={styles.container}>
                <View
                    style={{
                        position: 'absolute',
                        top: 0,
                        left: 0,
                        right: 0,
                        bottom: 0,
                        paddingLeft: 20,
                        flexDirection: 'row-reverse',
                    }}>
                    <IconButton
                        iconName={inList ? 'heart' : 'heart-o'}
                        iconSize={fontSize.extraBig}
                        iconColor={inList ? colors.red : colors.gray}
                        iconFont="FontAwesome"
                        onPress={() => {
                            onProductLike(product);
                        }}
                    />
                </View>
                <View style={styles.imageContainer}>
                    <Image
                        style={[styles.image]}
                        resizeMode="cover"
                        source={{ uri: product.img }}
                    />
                </View>
                <View style={styles.productContentContainer}>
                    <Text numberOfLines={1} style={styles.brand}>
                        {product.brand.toUpperCase()}
                    </Text>
                    <Text numberOfLines={1} style={styles.name}>
                        {product.name}
                    </Text>
                    <StarRating
                        disabled
                        maxStars={5}
                        rating={product.score / 2} // todo fix remove one products updated
                        starSize={13}
                        fullStarColor={colors.yellow}
                        containerStyle={{
                            justifyContent: 'flex-start',
                            paddingTop: 10,
                        }}
                        starStyle={{ paddingRight: 3 }}
                    />
                </View>
            </View>
        </TouchableWithoutFeedback>
    );
};

const styles = StyleSheet.create({
    container: {
        height: 250,
        width: 250,
        padding: spacing.big,
        marginLeft: 20,
    },
    imageContainer: {
        alignItems: 'center',
    },
    image: {
        width: 150,
        height: 150,
    },
    productContentContainer: {
        paddingTop: 10,
    },
    brand: {
        color: colors.gray,
        fontFamily: 'OpenSans',
        fontSize: fontSize.small,
    },
    name: {
        color: colors.darkGray,
        fontWeight: 'bold',
        fontSize: fontSize.big,
        fontFamily: 'OpenSansBold',
    },
});

export default withNavigation(ProductCardCarrousel);
