import ezapi from "../utils/ezapi";

const initialState = {};

export const Misc = (state = initialState, action) => {
    switch (action.type) {
        case 'get_meta':
            return {...action.payload};
        default:
            return state
    }
};

export const getMeta = () => {
    return async dispatch => {
        try {
            const {data: result} = await ezapi.get('/meta');
            dispatch({type: 'get_meta', payload: result});
        } catch (e) {
            alert("Query to server is faild: " +e.message + " (may be error of CORS)");
            console.log(e.message)
        }
    };
};