import React, { PureComponent } from 'react';
import {
    Animated,
    Image,
    Platform,
    Share,
    StyleSheet,
    View,
} from 'react-native';
import ParallaxScrollView from '../../app/other/ParallaxScrollView';
import { Header } from 'react-navigation-stack';
import { LinearGradient } from 'expo-linear-gradient';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Toast } from 'native-base';
import { SafeAreaConsumer } from 'react-native-safe-area-context';
import ProductDetails from './components/ProductDetails';
import Styles from '../../app/other/Styles';
import {
    getProductComments as getProductCommentsAction,
    getProductDetails as getProductDetailsAction,
} from '../../../store/actions/products_actions';
import { getRandomColor, isPng } from '../../../utils/functions';
import colors from '../../../constants/colors';
import i18n from '../../../utils/i18n';
import EzencielLoading from '../../app/views/Loading';
import TabsHorizontalList from './TabsHorizontalList';
import ProductMediaContent from './components/ProductMediaContent';
import spacing from '../../../constants/spacing';
import fontSize from '../../../constants/fontSize';

class ProductDetailsScreen extends PureComponent {
    static navigationOptions = {
        headerTransparent: true,
        title: null,
    };

    static getDerivedStateFromProps(props, state) {
        const { product } = state;
        let backgroundColor = colors.white;
        let productId = null;
        if (product && product.img) {
            backgroundColor = isPng(product.img)
                ? getRandomColor()
                : colors.white;
        }

        if (product) {
            productId = product._id;
        }

        return {
            backgroundColor,
            productId,
        };
    }

    constructor(props) {
        super(props);

        this.state = {
            page: 0,
            isContentLast: false,
            backgroundColor: colors.white,
            productId: null,
            product: null,
            loading: true,
            contentLoading: false,
            nextContentLoading: false,
            selectedIndex: 0,
            categories: [{ value: 'comments', name: i18n.t('comments') }],
            selectedTags: [],
            selectedCategory: null,
            content: [],
        };

        this.scrollAnim = new Animated.Value(0);
    }

    componentDidMount() {
        const { navigation } = this.props;

        // if (!productTags || !productTags.length) {
        //   getProductTags();
        // }

        this.navListener = navigation.addListener(
            'willFocus',
            this.willFocusListener,
        );

        this.scrollAnim.addListener(this.scrollAnimationListener);
    }

    componentWillUnmount() {
        this.navListener.remove();
        this.scrollAnim.removeAllListeners();
    }

    scrollAnimationListener = ({ value }) => {
        this.scrollAnimValue = value;

        return this.scrollAnimValue;
    };

    willFocusListener = () => {
        const { navigation } = this.props;
        const { productId } = this.state;
        const paramProductId = navigation.getParam('productId', null);

        if (paramProductId !== productId) {
            this.getProductData(paramProductId);
        }
    };

    handleLoadMoreContent = async () => {
        const { getProductComments } = this.props;
        const {
            productId,
            content: oldContent,
            selectedCategory,
            selectedTags,
            isContentLast,
            page,
            nextContentLoading,
        } = this.state;
        try {
            if (isContentLast || nextContentLoading) return;

            const { value, name: category } = selectedCategory;

            this.setState({ nextContentLoading: true });

            let content = [];
            if (value === 'comments') {
                content = await getProductComments(
                    productId,
                    selectedTags,
                    page + 1,
                );
            }

            if (!content || !content.length) {
                Toast.show({
                    text: i18n.t('product.reachedEnd', { category }),
                });

                this.setState({
                    isContentLast: true,
                });
                return;
            }

            this.setState({
                page: page + 1,
                content: [...oldContent, ...content],
            });
        } catch (err) {
            // catch err
        } finally {
            this.setState({ nextContentLoading: false });
        }
    };

    getProductData = async (productId) => {
        const {
            getProductDetails,
            // getProductMedia,
            getProductComments,
            navigation,
            // productTags
        } = this.props;

        const { categories } = this.state;
        try {
            // clearLastBV();
            this.setState({ loading: true });

            let newCategories = categories;
            // categories.length !== productTags.length + 1
            //     ? [...categories, ...productTags]
            //     : categories;

            const promises = newCategories.map((category) => {
                if (category.value === 'comments') {
                    return getProductComments(productId, [], 0);
                }

                // return getProductMedia(productId, [category], 0);
            });

            // clearLastBV();
            this.setState({ loading: true });

            const [product, ...contents] = await Promise.all([
                getProductDetails(productId),
                ...promises,
            ]);

            if (!product) {
                throw new Error(i18n.t('product.notFound'));
            }

            newCategories = newCategories
                .map((category, index) => {
                    return {
                        ...category,
                        count: contents[index].length,
                        content: contents[index],
                    };
                })
                .filter(({ count }) => count > 0);

            // const filteredContent = contents.filter(content => content.length > 0);
            return this.setState({
                product,
                content: newCategories.length ? newCategories[0].content : [],
                selectedIndex: 0,
                selectedCategory: newCategories[0],
                categories: newCategories,
            });
        } catch (err) {
            Toast.show({
                text: err.message || i18n.t('product.notFound'),
            });

            return navigation.pop();
        } finally {
            this.setState({ loading: false });
        }
    };

    isProductLiked = (productId) => {
        // const {
        //   User: { wishlist }
        // } = this.props;
        //
        // return !!wishlist.find(product => product._id === productId) || false;
    };

    onProductLike = (item, isLiked) => {
        // try {
        //   const { addToWishList, removeFromWishList } = this.props;
        //   if (!isLiked) {
        //     addToWishList(item);
        //
        //     return Toast.show({
        //       text: i18n.t('product.addToWishlistSuccess')
        //     });
        //   }
        //
        //   removeFromWishList(item._id);
        //
        //   return Toast.show({
        //     text: i18n.t('product.removeToWishlistSuccess')
        //   });
        // } catch (err) {
        //   return Toast.show({
        //     text: !isLiked
        //         ? i18n.t('product.addToWishlistError')
        //         : i18n.t('product.removeToWishlistError')
        //   });
        // }
    };

    onShare = () => {
        // eslint-disable-next-line
        const { product } = this.state;
        Share.share({
            title: i18n.t('shareTitle'),
            message: i18n.t('shareMessage'),
        })
            .then(this.onShareSuccess)
            .catch(this.onShareError);
    };

    onShareSuccess = () => {
        Toast.show({
            text: i18n.t('shareSuccess'),
        });
    };

    onShareError = () => {
        Toast.show({
            text: i18n.t('shareError'),
        });
    };

    changeSelectedTab = (selectedIndex) => {
        const { categories } = this.state;

        this.setState({
            selectedIndex,
            selectedCategory: categories[selectedIndex],
            content: categories[selectedIndex].content,
            page: 0,
            isContentLast: false,
        });
    };

    selectTag = (selectedTags) => {
        this.setState({ content: [] }, () => {
            this.setState({ selectedTags, page: 0 }, this.loadContent);
        });
    };

    loadContent = async () => {
        const { getProductComments } = this.props;
        const { productId, selectedCategory, selectedTags } = this.state;
        const { value } = selectedCategory;
        try {
            this.setState({ contentLoading: true });
            let content = [];
            if (value === 'comments') {
                content = await getProductComments(productId, selectedTags, 0);
            }

            if (!content || !content.length) {
                content = [];
            }

            this.setState({
                content,
                isContentLast: !content.length,
            });
        } catch (err) {
            // catch err
        } finally {
            this.setState({ contentLoading: false });
        }
    };

    renderTopTabs = () => {
        const { selectedIndex, categories } = this.state;
        const data = categories.map(({ name }) => name);

        return (
            <View>
                <TabsHorizontalList
                    style={styles.tabsStyle}
                    data={data}
                    selectedChanged={this.changeSelectedTab}
                    activeTab={selectedIndex}
                />
            </View>
        );
    };

    renderContent = () => {
        const {
            content,
            contentLoading,
            product,
            selectedCategory,
        } = this.state;

        return (
            <ProductMediaContent
                content={content}
                refreshing={contentLoading}
                onRefresh={this.loadContent}
                product={product}
                refreshControl={
                    contentLoading ? (
                        <View style={{ flex: 1, backgroundColor: 'white' }}>
                            <EzencielLoading />
                        </View>
                    ) : null
                }
                category={selectedCategory.value}
                onSelectedTagsChange={this.selectTag}
            />
        );
    };

    renderStickyHeader = (opacity, paddingTop) => {
        const { product } = this.state;
        if (!product) return null;

        return (
            <View style={[styles.headerContainer, { paddingTop }]}>
                <Animated.Text
                    style={[styles.headerTitle, { opacity }]}
                    numberOfLines={1}>
                    {product.name}
                </Animated.Text>
            </View>
        );
    };

    renderBackground = () => {
        const { backgroundColor, product } = this.state;
        if (!product) return null;

        return (
            <View style={[styles.imageContainer, { backgroundColor }]}>
                <Image source={{ uri: product.img }} style={styles.image} />

                <LinearGradient
                    colors={[
                        'rgba(255,255,255,0)',
                        'rgba(16,16,16,0.56)',
                        'rgba(0,0,0,0.76)',
                    ]}
                    locations={[0, 0.78, 1]}
                    style={styles.gradient}
                />
            </View>
        );
    };

    //
    onScrollBeginDrag = () => {
        // const { layoutMeasurement: lm, contentOffset, contentSize } = nativeEvent;
        // console.log('nativeEvent', nativeEvent);
        // const { enableTopTabsScroll } = this.state;
        // const enableTopTabsScrollNew = lm.height + contentOffset.y >= contentSize.height;
        // if (enableTopTabsScroll !== enableTopTabsScrollNew) {
        //   this.setState({
        //     enableTopTabsScroll: enableTopTabsScrollNew
        //   });
        // }
    };

    onMomentumScrollEnd = ({
        nativeEvent: { layoutMeasurement: lm, contentOffset, contentSize },
    }) => {
        if (lm.height + contentOffset.y >= contentSize.height - lm.height / 2) {
            this.handleLoadMoreContent();
        }
    };

    computeHeaderHeight = (top) => {
        const defaultHeaderHeight = Header.HEIGHT + top;

        if (Platform.OS === 'ios') return defaultHeaderHeight - 20;

        return defaultHeaderHeight;
    };

    render() {
        const { backgroundColor, product, loading, productId } = this.state;
        const titleOpacity = this.scrollAnim.interpolate({
            inputRange: [270, 320],
            outputRange: [0, 1],
            extrapolate: 'clamp',
        });

        if (loading) {
            return (
                <View style={styles.activityIndicatorContainer}>
                    <EzencielLoading />
                </View>
            );
        }

        const isLiked = this.isProductLiked(productId);

        return (
            <SafeAreaConsumer style={styles.flex1}>
                {(insets) => (
                    <View style={Styles.container}>
                        <View style={Styles.subContainer}>
                            <ParallaxScrollView
                                showsVerticalScrollIndicator={false}
                                style={[
                                    styles.flex1,
                                    {
                                        backgroundColor: 'white',
                                        paddingBottom: insets.bottom,
                                    },
                                ]}
                                backgroundColor={backgroundColor}
                                stickyHeaderHeight={this.computeHeaderHeight(
                                    insets.top,
                                )}
                                renderStickyHeader={() =>
                                    this.renderStickyHeader(
                                        titleOpacity,
                                        insets.top,
                                    )
                                }
                                parallaxHeaderHeight={280}
                                renderBackground={this.renderBackground}
                                onScroll={Animated.event([
                                    {
                                        nativeEvent: {
                                            contentOffset: {
                                                y: this.scrollAnim,
                                            },
                                        },
                                    },
                                ])}
                                scrollEventThrottle={500}
                                onScrollBeginDrag={this.onScrollBeginDrag}
                                onMomentumScrollEnd={this.onMomentumScrollEnd}>
                                <View style={styles.container}>
                                    <ProductDetails
                                        product={product}
                                        liked={isLiked}
                                        backgroundColor={backgroundColor}
                                        onProductLike={() =>
                                            this.onProductLike(product, isLiked)
                                        }
                                        onShare={this.onShare}
                                    />
                                    {this.renderTopTabs()}
                                    {this.renderContent()}
                                </View>
                            </ParallaxScrollView>
                        </View>
                    </View>
                )}
            </SafeAreaConsumer>
        );
    }
}

const styles = StyleSheet.create({
    flex1: {
        flex: 1,
        display: 'flex',
        justifyContent: 'center',
        flexDirection: 'row',
    },
    container: {
        backgroundColor: colors.lightGray,
    },
    activityIndicatorContainer: {
        flex: 1,
        alignItems: 'center',
        justifyContent: 'center',
        paddingVertical: 15,
        backgroundColor: 'white',
    },
    imageContainer: {
        height: 280,
        width: '100%',
        alignItems: 'center',
    },
    image: {
        width: '50%',
        height: 280,
        marginTop: 40,
    },
    gradient: {
        position: 'absolute',
        bottom: 0,
        width: '100%',
        height: 80,
        opacity: 0.35,
    },
    headerContainer: {
        width: '100%',
        height: '100%',
        alignItems: 'center',
        justifyContent: 'center',
        paddingLeft: spacing.extraHuge,
        paddingRight: spacing.extraBig,
        borderBottomWidth: 1,
        borderColor: colors.lightGray,
    },
    headerTitle: {
        fontFamily: 'OpenSansBold',
        fontSize: fontSize.huge,
        color: colors.extraDarkGray,
    },
    tabsStyle: {
        flex: 1,
        justifyContent: 'flex-start',
        backgroundColor: 'white',
        maxHeight: 40,
        paddingHorizontal: 15,
        borderColor: '#ddd',
        borderBottomWidth: StyleSheet.hairlineWidth,
    },
    refreshContainer: {
        alignItems: 'center',
        paddingVertical: 15,
        backgroundColor: '#fff',
    },
});

function mapStateToProps(state) {
    return {
        User: state.user,
        // productTags: state.Misc.productTags
        // Products: state.Products
    };
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators(
        {
            // getProductBlogs: getProductBlogsAction,
            // getProductVideos: getProductVideosAction,
            getProductDetails: getProductDetailsAction,
            // getProductTags: getProductTagsAction,
            // getProductMedia: getProductMediaAction,
            getProductComments: getProductCommentsAction,
        },
        dispatch,
    );
}

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(ProductDetailsScreen);
