import React, { useEffect } from 'react';
import { StyleSheet, YellowBox, Platform } from 'react-native';
import { registerRootComponent } from 'expo';
import { Provider } from 'react-redux';
import { Root } from 'native-base';
import { SafeAreaProvider } from 'react-native-safe-area-context';
import App from './App';
import { ConfigureStore } from './store/config';
import { setTopLevelNavigator } from './utils/navigator';

const store = ConfigureStore;
const RootComponent = () => {
    const warnings_ignore = [
        'measureLayoutRelativeToContainingList',
        'Setting a timer',
    ];
    YellowBox.ignoreWarnings(warnings_ignore);
    console.ignoredYellowBox = [warnings_ignore];

    return (
        <Provider store={store}>
            <SafeAreaProvider>
                <Root>
                    <App ref={(ref) => setTopLevelNavigator(ref)} />
                </Root>
            </SafeAreaProvider>
        </Provider>
    );
};

registerRootComponent(RootComponent);

const styles = StyleSheet.create({
    container: {
        flex: 1,
        backgroundColor: 'white',
        alignItems: 'center',
        justifyContent: 'center',
    },
});
