import React, {PureComponent} from 'react';
import {Image, View, Text, TouchableOpacity, TouchableWithoutFeedback} from 'react-native';
import {withNavigation} from 'react-navigation';
import AuthorAvatar from '../../../author/AuthorAvatar';
import Video from '../../../media/Video';
import {calculateTimePassed, generateKey} from '../../../../utils/functions';
import styles from './styles/Videos.style';

// Videos component showing list of Videos

class Content extends PureComponent {
    constructor(props) {
        super(props);

        this.state = {
            imageError: false
        };
    }

    errorLoadingImage = err => {
        const {item} = this.props;
        this.setState({imageError: true});
    };

    navigateToAuthor = (author) => {
        const {navigation} = this.props;
        navigation.navigate('Author', {author: author, fromRoute: navigation.state.routeName})
    };

    render() {
        const {item, index, hideImage} = this.props;
        const {imageError} = this.state;
        const {title, author, summary, image, link, date} = item;
        const dateTime = calculateTimePassed(date);
        const isVideo = link ? link.includes('youtube') : false;

        return (
            <View style={styles.videoContainer} key={`${index}-${generateKey(title)}`}>
                <Text style={styles.date}>{dateTime}</Text>

                {author && author.name && author.link && (
                    <TouchableWithoutFeedback
                        onPress={() => this.navigateToAuthor(author)}
                    >
                        <View>
                            <AuthorAvatar data={author}/>
                        </View>
                    </TouchableWithoutFeedback>
                )}

                <Text style={styles.title}>{title}</Text>

                <Text style={styles.description}>{summary}</Text>

                {isVideo ? (
                    <Video image={image} videoLink={link} title={title}/>
                ) : (
                    image &&
                    !hideImage &&
                    !imageError && (
                        <Image
                            style={styles.image}
                            source={{uri: image}}
                            onError={this.errorLoadingImage}
                        />
                    )
                )}
            </View>
        );
    }
}

export default withNavigation(Content);
