import React from 'react';
import {Image, StyleSheet, Text, TouchableOpacity, TouchableWithoutFeedback, View} from 'react-native';
import colors from '../../constants/colors';
import fontSize from '../../constants/fontSize';
import spacing from "../../constants/spacing";
import Icon from "../app/icons/Icon";
import Spacer from "../app/other/Spacer";
import IconButton from "../app/buttons/IconButton";

const SearchCard = ({image, supperTitle, title, children, caption, inList, onLike, onPress, seeMedia, showYoutube, roundImage}) => {
    return (
        <View style={styles.container}>
            <TouchableWithoutFeedback onPress={onPress}>
                <View style={styles.itemsContainer}>
                    <View style={styles.imageContainer}>
                        {image ?
                            <Image style={roundImage ? {width: 100, height: 100, borderRadius: 50} : {
                                width: 125,
                                height: 125
                            }}
                                   resizeMode='cover' source={{uri: image}}/> :
                            <View style={{
                                width: 100, height: 100, borderRadius: 50,
                                backgroundColor: colors.lightGray,
                            }}/>
                        }
                    </View>
                    <View style={styles.productContentContainer}>
                        <Text numberOfLines={1}
                              style={styles.supertitle}>{supperTitle ? supperTitle.toUpperCase() : null}</Text>

                        <View style={{flexDirection: 'row', alignItems: 'center'}}>
                            <Text numberOfLines={2} style={styles.title}>{title}</Text>
                            <Spacer h={5}/>
                            {showYoutube ?
                                <Icon name='youtube' font='FontAwesome' size={fontSize.medium} color={colors.gray}/>
                                : null}
                        </View>
                        {children}
                        <TouchableOpacity onPress={seeMedia}>
                            <View style={{flexDirection: 'row', paddingTop: 2}}>
                                <Icon name='folder-video' font='Entypo' size={fontSize.medium} color={colors.gray}/>
                                <Spacer h={5}/>
                                <Text numberOfLines={2} style={styles.caption}>{caption}</Text>
                            </View>
                        </TouchableOpacity>
                    </View>
                    <View>
                        <IconButton
                            iconName={inList ? 'heart' : 'heart-o'}
                            iconSize={fontSize.big}
                            iconColor={inList ? colors.red : colors.gray}
                            iconFont="FontAwesome"
                            containerStyle={{padding: 0}}
                            onPress={onLike}
                        />
                    </View>
                </View>
            </TouchableWithoutFeedback>
        </View>
    );
};

const styles = StyleSheet.create({
    container: {
        height: 160,
        justifyContent: 'center',
        marginRight: spacing.big
    },
    itemsContainer: {
        flex: 1,
        flexDirection: 'row',
        padding: spacing.medium,
    },
    imageContainer: {
        justifyContent: 'center',
        alignItems: 'center',
        flex: 2,
    },
    productContentContainer: {
        flex: 3,
        flexDirection: 'column',
        justifyContent: 'flex-end',
        paddingLeft: 20,
    },
    supertitle: {
        color: colors.gray,
        fontFamily: 'OpenSans',
        fontSize: fontSize.small
    },
    title: {
        color: colors.darkGray,
        fontWeight: 'bold',
        fontSize: fontSize.big,
        fontFamily: 'OpenSansBold',
    },
    caption: {
        fontSize: fontSize.small,
        fontFamily: 'OpenSans',
        color: colors.gray
    },
});


export default SearchCard;
