import React from 'react';
import { Text, View, StyleSheet } from 'react-native';
import fontSize from '../../../constants/fontSize';
import colors from '../../../constants/colors';
import i18n from '../../../utils/i18n';

// Description component with default styling
// pass description prop to display description
// pass style prop to style description container

const styles = StyleSheet.create({
  container: {
    flex: 1
  },
  text: {
    width: '90%',
    fontSize: fontSize.semiMedium,
    fontFamily: 'OpenSans',
    color: colors.default
  }
});

const Description = ({ style, textStyle, numberOfLines, description }) => (
  <View style={[styles.container, style]}>
    <Text style={[styles.text, textStyle]} numberOfLines={numberOfLines}>
      {description || i18n.t('product.noDescription')}
    </Text>
  </View>
);

export default Description;
