import api from "../utils/ezapi";

export const AuthorInfo = (state = null, action) => {
    switch (action.type) {
        case 'update_authorInfo':
            return action.payload
        default:
            return state
    }
};

export const getAuthorInfo = (id) => {
    return async dispatch => {
        try {
            const data = {
                author_id: id
            };
            const {data: result} = await api.post('/author', data);
            console.log(`/author ${id}`)

            // adjust domain
            if (result && result.domain) {
                result.domainInfo = {
                    _id: result.domain,
                    link: 'http://' + result.domain,
                    text: result.domain
                }
            }
            dispatch({type: 'update_authorInfo', payload: result})
        } catch (e) {
            console.log(e.message)
        }
    };
};