import React from 'react';
import { View, Text, StyleSheet } from 'react-native';
import IconText from './IconText';
import { getRatingColor } from '../../../utils/functions';
import i18n from '../../../utils/i18n';
import fontSize from '../../../constants/fontSize';
import colors from '../../../constants/colors';
import spacing from '../../../constants/spacing';

// RankingMediaSentiment component showing data as 3 IconText components
// pass style prop to change it's default styling
// sentiment must be one of these 'Loved','Neutral','Sad'

const styles = StyleSheet.create({
  container: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center'
  },
  text: {
    fontSize: fontSize.small,
    fontFamily: 'OpenSans',
    color: colors.default
  },
  scoretext: {
    marginLeft: spacing.big,
    fontSize: fontSize.small,
    fontFamily: 'OpenSansBold'
  }
});

const RankingMediaSentiment = props => {
  const { ranking, price, score, style, iconSize, textStyle } = props;

  let productRating = 7;
  if (score) productRating = score.toFixed(1);

  // 10 - 8.75 green
  // 8.75 - 7.5 light green
  // 6.5 - 7.5 yellow
  // 5 - 6.5 // dark yellow
  // 5 red
  const color = getRatingColor(productRating);

  return (
    <View style={[styles.container, style]}>
      {/* {!isNaN(media) &&
                <IconText
                    iconName='message-square'
                    iconSize={iconSize}
                    text={i18n.t('media', { media })}
                    textStyle={[styles.text, textStyle]}
                />
            } */}

      {ranking && (
        <IconText
          iconName="bar-chart-2"
          iconSize={iconSize}
          text={i18n.t('rank', { ranking })}
          textStyle={[styles.text, textStyle]}
        />
      )}

      {price && <Text style={[styles.text, textStyle]}>{price}</Text>}

      {score && (
        <Text style={[styles.scoretext, { color }, textStyle]}>{productRating}</Text>
      )}
    </View>
  );
};

export default RankingMediaSentiment;
