import React from 'react';
import { View, Text, StyleSheet } from 'react-native';
import Icon from '../../app/icons/Icon';
import fontSize from '../../../constants/fontSize';
import colors from '../../../constants/colors';
import spacing from '../../../constants/spacing';

// Icon Text Component
// pass text prop to set text
// pass iconName, iconSize, iconColor, iconFont props to setup Icon
// pass containerStyle prop to style IconText container
// pass textStyle prop to style Text

const styles = StyleSheet.create({
  container: {
    flexDirection: 'row',
    alignItems: 'center'
  },
  text: {
    fontSize: fontSize.small,
    marginLeft: spacing.extraSmall,
    marginRight: spacing.big
  }
});

const IconText = ({
  iconName,
  iconSize,
  iconColor,
  iconFont,
  containerStyle,
  textStyle,
  text
}) => {
  return (
    <View style={[styles.container, containerStyle]}>
      <Icon name={iconName} size={iconSize} color={iconColor} font={iconFont} />

      <Text style={[styles.text, textStyle]}>{text.toString()}</Text>
    </View>
  );
};

export default IconText;
