/*
Header Scrollable view that animates the header to appear and disappear as the user scrolls
 */
import React, { PureComponent } from 'react';
import { Animated, StyleSheet, View, FlatList, Platform } from 'react-native';
import colors from '../../../constants/colors';
import { withNavigation } from 'react-navigation';
import Loading from './Loading';
import Styles from '../other/Styles';

class StickyHeaderView extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            height: null,
            showImages: {},
        };
        this.resetScroll = 0;
        this.flatListRef = null;
        this.scrollUp = 0;
    }

    setupAnimation(height) {
        this.scroll = new Animated.Value(0);
        this.offsetAnim = new Animated.Value(0);
        this.headerScroll = Animated.diffClamp(
            Animated.add(
                this.scroll.interpolate({
                    inputRange: [0, 1],
                    outputRange: [0, 1],
                    extrapolateLeft: 'clamp',
                }),
                this.offsetAnim,
            ),
            0,
            height,
        );
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        // scrolls up when prop passed
        const { navigation } = this.props;
        const scrollUp = navigation.getParam('scrollUp');
        if (scrollUp && scrollUp > this.scrollUp && this.flatListRef) {
            this.scrollUp = scrollUp;
            this.flatListRef._component.scrollToOffset({
                animated: true,
                offset: 0,
            });
        }
    }

    render() {
        const {
            navigation,
            data,
            empty,
            loadMore,
            loading,
            children,
            renderItem,
            resetScroll,
            page,
            isHomeView,
        } = this.props;
        // used to manage logic on navigation bar show or not show while scrolling
        let contentOffsetY;
        const headerTranslate = this.headerScroll
            ? this.headerScroll.interpolate({
                  inputRange: [0, this.state.height],
                  outputRange: [0, -this.state.height],
                  extrapolate: 'clamp',
              })
            : 0;

        //reset scroll value
        if (this.scroll && resetScroll && resetScroll !== this.resetScroll) {
            this.scroll.setValue(0);
            this.resetScroll = resetScroll;
        }

        // settings for home
        const settings = {
            initialNumToRender: isHomeView ? 3 : 10,
            onEndReachedThreshold: 0.8,
            windowSize: isHomeView ? 3 : 10,
            maxToRenderPerBatch: isHomeView ? 3 : 10,
        };
        const options = {
            ref: (c) => (this.flatListRef = c),
            load: true,
            data: data,
            renderItem: renderItem,
            keyExtractor: (item) => item._id,
            ListEmptyComponent: empty,
            onEndReached: loadMore,
            initialNumToRender: settings.initialNumToRender,
            maxToRenderPerBatch: settings.maxToRenderPerBatch,
            onEndReachedThreshold: settings.onEndReachedThreshold,
            windowSize: settings.windowSize,
            removeClippedSubviews: true,
            scrollEventThrottle: 16,
            refreshing: loading,
            showsVerticalScrollIndicator: false,
            contentContainerStyle: [
                styles.flatListContainer,
                {
                    paddingTop:
                        Platform.OS === 'web'
                            ? this.state.height - 20
                            : this.state.height,
                    paddingBottom: 50,
                },
            ],
            onScroll: Animated.event(
                [{ nativeEvent: { contentOffset: { y: this.scroll } } }],
                {
                    useNativeDriver: true,
                },
            ),
            onScrollBeginDrag: ({ nativeEvent }) => {
                contentOffsetY = nativeEvent.contentOffset.y;
            },
            onScrollEndDrag: ({ nativeEvent }) => {
                navigation.setParams({
                    visible: contentOffsetY >= nativeEvent.contentOffset.y,
                });
            },
            ListFooterComponent: () =>
                loading ? (
                    <View
                        style={{
                            padding: 50,
                            paddingBottom: 90,
                            backgroundColor: colors.white,
                            marginBottom: 0,
                        }}>
                        <Loading />
                    </View>
                ) : null,
        };

        return (
            <View style={Styles.container}>
                <View style={Styles.subContainer}>
                    <Animated.View
                        style={[
                            Styles.header,
                            {
                                position: 'absolute',
                                zIndex: 1,
                                transform: [{ translateY: headerTranslate }],
                            },
                        ]}
                        onLayout={(event) => {
                            var { height } = event.nativeEvent.layout;
                            this.setState({ height: height });
                            this.setupAnimation(height);
                        }}>
                        {children}
                    </Animated.View>
                    {this.state.height ? (
                        loading && page === 0 ? (
                            <Loading />
                        ) : Platform.OS === 'web' ? (
                            <FlatList {...options} />
                        ) : (
                            <Animated.FlatList {...options} />
                        )
                    ) : null}
                </View>
            </View>
        );
    }
}

export default withNavigation(StickyHeaderView);

const styles = StyleSheet.create({
    flatListContainer: {
        backgroundColor: colors.white,
    },
});
