import React, {useState} from 'react';
import {Image, StyleSheet, Text, TouchableWithoutFeedback, View} from 'react-native';
import colors from '../../constants/colors';
import fontSize from '../../constants/fontSize';
import spacing from "../../constants/spacing";
import {withNavigation} from "react-navigation";
import Video from './Video';
import AuthorAvatar from "../author/AuthorAvatar";
import moment from "moment";
import {openWebView} from "../../utils/navigator";

const MediaCardCarrousel = ({navigation, data}) => {
    const [state, setState] = useState({imageError: false});
    const isVideo = data.link.includes('youtube');
    const dateTime = moment(data.date).fromNow();

    const errorLoadingImage = () => {
        setState({...state, imageError: true});
    };

    return (
        <TouchableWithoutFeedback onPress={() => openWebView(data.link, {title: data.title}, {
            type: 'blog',
            author: data.author._id,
            title: data.title
        })}>
            <View style={styles.container}>
                <View style={styles.imageContainer}>
                    {isVideo ? (
                        <Video image={data.image} videoLink={data.link}/>
                    ) : (
                        data.image && !state.imageError && (
                            <Image
                                style={styles.image}
                                source={{uri: data.image}}
                                onError={errorLoadingImage}
                            />
                        )
                    )}
                </View>
                <View>
                    <TouchableWithoutFeedback onPress={() => navigation.navigate('Author', {
                        author: data.author,
                        fromRoute: navigation.state.routeName
                    })}>
                        <View style={{paddingTop: spacing.big}}>
                            <AuthorAvatar data={data.author}/>
                        </View>
                    </TouchableWithoutFeedback>
                    <Text style={styles.date}>{dateTime}</Text>
                    <Text numberOfLines={2} style={styles.title}>{data.title}</Text>
                    <Text numberOfLines={2} style={styles.description}>{data.summary}</Text>
                </View>
            </View>
        </TouchableWithoutFeedback>
    );
};

const styles = StyleSheet.create({
    container: {
        width: 350,
        marginLeft: spacing.big,
        marginRight: spacing.big
    },
    imageContainer: {
        alignItems: 'center',
    },
    image: {
        width: '100%',
        height: 300,
        borderRadius: 5,
    },
    brand: {
        color: colors.gray,
        fontFamily: 'OpenSans',
        fontSize: fontSize.small
    },
    title: {
        paddingTop: spacing.medium,
        color: colors.darkGray,
        fontFamily: 'OpenSansBold',
        fontSize: fontSize.big,

    },
    description: {
        marginTop: spacing.medium,
        fontFamily: 'OpenSans',
        fontSize: fontSize.medium,
        color: colors.darkGray,
        lineHeight: 20,
    },
    date: {
        fontSize: fontSize.extraSmall,
        fontFamily: 'Roboto',
        color: colors.categoriesLightGray,
        paddingTop: 7
    },
});


export default withNavigation(MediaCardCarrousel);
