import React, {PureComponent} from 'react';
import {View, TouchableWithoutFeedback, Image, StyleSheet} from 'react-native';
import Icon from '../app/icons/Icon';
import colors from '../../constants/colors';
import {openWebView} from '../../utils/navigator';
import spacing from "../../constants/spacing";

class Video extends PureComponent {
    openWebView = () => {
        const {videoLink, title} = this.props;

        openWebView(`${videoLink}?mute=0&rel=0&autoplay=0&showinfo=0&controls=0`, {title}, {
            type: 'video',
            title: title
        });
    };

    render() {
        const {image} = this.props;

        return (
            <TouchableWithoutFeedback onPress={this.openWebView}>
                <View>
                    <Image resizeMode="cover" style={styles.image} source={{uri: image}}/>
                    <View style={styles.iconContainer}>
                        <Icon
                            name="youtube-play"
                            size={50}
                            color={image.includes('live.jpg') ? colors.red: colors.transparentWhite}
                            font="FontAwesome"
                        />
                    </View>
                </View>
            </TouchableWithoutFeedback>
        );
    }
}

const styles = StyleSheet.create({
    container: {},
    image: {
        width: '100%',
        aspectRatio: 16 / 9,
        marginTop: spacing.medium
    },
    iconContainer: {
        width: '100%',
        height: '100%',
        position: 'absolute',
        justifyContent: 'center',
        alignItems: 'center'
    }
});

export default Video;
