/* eslint-disable react/jsx-props-no-spreading */
import React from 'react';
import { View, Text, StyleSheet } from 'react-native';
import IconButton from './IconButton';
import fontSize from '../../../constants/fontSize';
import colors from '../../../constants/colors';
import spacing from '../../../constants/spacing';

const styles = StyleSheet.create({
    textContainer: {
        flexDirection: 'row',
    },
    text: {
        fontFamily: 'Roboto',
        fontSize: fontSize.small,
        color: colors.darkGray,
        fontWeight: 'bold',
        marginLeft: spacing.medium,
    },
    extraText: {
        fontFamily: 'Roboto',
        fontSize: fontSize.small,
        color: colors.darkGray,
        fontWeight: 'bold',
        marginRight: spacing.small,
        marginLeft: spacing.small,
    },
});

// Icon Text Button Component
// pass textStyle prop to change text style
// pass text prop to display that text
// pass extraText prop to dispaly additional text next no exsiting
// check IconButton comment for other neccessery props

const IconTextButton = ({
    textContainerStyle,
    textStyle,
    text,
    extraText,
    extraTextStyle,
    ...iconButtonProps
}) => {
    return (
        <IconButton {...iconButtonProps}>
            <View style={[styles.textContainer, textContainerStyle]}>
                <Text style={[styles.text, textStyle]}>{text}</Text>

                {extraText && (
                    <Text style={[styles.extraText, extraTextStyle]}>
                        {extraText}
                    </Text>
                )}
            </View>
        </IconButton>
    );
};

IconTextButton.defaultProps = {
    extraText: null,
    textStyle: null,
    textContainerStyle: null,
    extraTextStyle: null,
};

export default IconTextButton;
