import React, { useEffect, useState } from 'react';
import {
    Platform,
    StyleSheet,
    Switch,
    Text,
    TextInput,
    View,
    ScrollView,
} from 'react-native';
import spacing from '../../constants/spacing';
import colors from '../../constants/colors';
import fontSize from '../../constants/fontSize';
import Styles from '../app/other/Styles';
import TextButton from '../app/buttons/TextButton';
import { createList, updateList } from '../../store/lists';
import { Toast } from 'native-base';
import { useDispatch } from 'react-redux';

export const ListForm = ({ list, user, onSubmit, item, listType }) => {
    const dispatch = useDispatch();
    const initial = {
        name: '',
        description: '',
        isPublic: true,
        type: null,
        error: null,
    };
    const [state, setState] = useState(initial);

    useEffect(() => {
        list ? setState({ ...list }) : setState(initial);
    }, [list]);

    const Submit = () => {
        if (!state.name || state.name === '') {
            setState({ ...state, error: 'You are missing the list name!' });
        } else {
            let msg;
            if (list) {
                dispatch(updateList(user.uid, state));
                msg = `${state.name} updated`;
            } else {
                state.type = listType;
                dispatch(createList(user.uid, item, state));
                msg = `${state.name} created`;
            }
            setTimeout(() => {
                Toast.show({ text: msg, buttonText: 'Okay', duration: 2000 });
            }, 500);
            onSubmit();
        }
    };

    return (
        <View style={styles.content}>
            <ScrollView>
                <Text style={styles.label}>List name</Text>
                <TextInput
                    style={[styles.input, { minHeight: 30 }]}
                    onChangeText={(name) =>
                        setState({ ...state, name: name, error: null })
                    }
                    value={state.name}
                />
                <Text style={Styles.error}>
                    {state.error ? state.error : null}
                </Text>
                <Text style={styles.label}>Description</Text>
                <TextInput
                    style={[styles.input, { height: 60 }]}
                    onChangeText={(input) =>
                        setState({ ...state, description: input })
                    }
                    value={state.description}
                    multiline
                />
                <View style={{ flexDirection: 'row', alignItems: 'center' }}>
                    <Text style={[styles.label, { flex: 1 }]}>
                        Make private
                    </Text>
                    <Switch
                        value={!state.isPublic}
                        onValueChange={() =>
                            setState({ ...state, isPublic: !state.isPublic })
                        }
                        trackColor={{
                            true: colors.gray,
                            false: colors.lightGray,
                        }}
                        thumbColor={[colors.white]}
                        ios_backgroundColor="#fbfbfb"
                    />
                </View>
                <View style={styles.actionsSection}>
                    <TextButton
                        style={styles.submitButton}
                        textStyle={styles.submitButtonText}
                        onPress={Submit}
                        text={list ? 'Save List' : 'Create List'}
                    />
                </View>
            </ScrollView>
        </View>
    );
};

const styles = StyleSheet.create({
    content: {
        padding: 10,
        paddingBottom: 0,
    },
    input: {
        textAlignVertical: 'top',
        borderRadius: spacing.medium,
        backgroundColor: colors.lighterGray,
        color: colors.darkGray,
        fontFamily: 'OpenSans',
        fontSize: fontSize.medium,
        padding: spacing.big,
        marginBottom: 10,
    },
    label: {
        fontFamily: 'OpenSansSemiBold',
        fontSize: fontSize.medium,
        color: colors.black,
        paddingBottom: 5,
        marginLeft: 5,
    },
    actionsSection: {
        justifyContent: 'flex-end',
        flexDirection: 'row',
        borderTopColor: colors.lightGray,
        borderTopWidth: 1,
        paddingTop: 15,
        marginTop: 20,
        marginBottom: Platform.OS === 'ios' ? 10 : 5,
    },
    submitButton: {
        backgroundColor: colors.red,
        height: 35,
        borderRadius: 3,
        flex: 1,
    },
    submitButtonText: { color: colors.white, fontFamily: 'OpenSansSemiBold' },
});

export default ListForm;
