import ezapi from '../utils/ezapi';
import differenceBy from 'lodash/differenceBy';

export const initialParams = {
    type: 'media',
    term: '',
    tags: [],
    product: [],
    author: [],
    following: [],
    items: [],
    tagName: null,
    tagType: null,
    time: 0,
    isLast: false,
    page: 0,
};

const initialSearch = {
    last: initialParams,
    previous: initialParams,
    stack: [],
    loading: false,
};

export const AuthorViewSearch = (state = initialParams, action) => {
    switch (action.type) {
        case 'search_author':
            return { ...state, ...action.payload, loading: false };
        case 'search_loading_author':
            return { ...state, ...action.payload, loading: true };
        case 'search_next_author':
            const newData = differenceBy(
                action.payload.items,
                state.items,
                '_id',
            );
            return {
                ...state,
                ...action.payload,
                items: [...state.items, ...newData],
                loading: false,
            };
        default:
            return state;
    }
};

export const HomeSearch = (state = initialSearch, action) => {
    switch (action.type) {
        case 'search_home':
            // either save as new item in stack or replace
            const stack = action.reset
                ? { stack: [] }
                : action.stack
                ? { stack: [...state.stack, state.previous] }
                : {};
            return {
                ...state,
                ...stack,
                last: { ...state.last, ...action.payload },
                loading: false,
            };
        case 'search_back_home':
            const previous = state.stack.pop();
            return { last: { ...previous }, stack: [...state.stack] };
        case 'search_loading_home':
            return {
                ...state,
                last: { ...state.last, ...action.payload },
                previous: { ...state.last },
                loading: true,
            };
        case 'search_next_home':
            const newData = differenceBy(
                action.payload.items,
                state.last.items,
                '_id',
            );
            return {
                ...state,
                last: {
                    ...state.last,
                    ...action.payload,
                    items: [...state.last.items, ...newData],
                },
                loading: false,
            };
        default:
            return state;
    }
};

export const Search = (state = initialSearch, action) => {
    switch (action.type) {
        case 'search':
            // either save as new item in stack or replace
            const validStack = state.previous.items.length > 0; // only stacks non empty searches
            const stack = action.reset
                ? { stack: [] }
                : action.stack && validStack
                ? { stack: [...state.stack, state.previous] }
                : {};
            return {
                ...state,
                ...stack,
                last: { ...state.last, ...action.payload },
                loading: false,
            };
        case 'search_back':
            const previous = state.stack.pop();
            return { last: { ...previous }, stack: [...state.stack] };
        case 'search_loading':
            return {
                ...state,
                last: { ...state.last, ...action.payload },
                previous: { ...state.last },
                loading: true,
            };
        case 'search_next':
            const newData = differenceBy(
                action.payload.items,
                state.last.items,
                '_id',
            );
            return {
                ...state,
                last: {
                    ...state.last,
                    ...action.payload,
                    items: [...state.last.items, ...newData],
                },
                loading: false,
            };
        default:
            return state;
    }
};

export const search = ({ params, stack = false, view = '' }) => {
    return async (dispatch) => {
        try {
            const type = params && params.type ? params.type : 'all';
            const author =
                params.author.length && params.following.length
                    ? intersect(params.author, params.following)
                    : params.author.length
                    ? params.author
                    : params.following;
            const time = params.page === 0 ? Date.now() : params.time;
            const data = {
                search: params && params.term ? params.term : null,
                type:
                    type === 'all' ? ['media', 'authors', 'products'] : [type],
                tags: params.tags,
                author: author,
                product: params.product,
                page: params.page,
                time: time,
            };

            // enabling loading
            dispatch({
                type: 'search_loading' + view,
                payload: { ...params },
                stack: stack,
                reset: type === 'all',
            });

            const { data: result } = await ezapi.post('/search', data);

            const payload = {
                ...params,
                items: result,
                isLast: result.length === 0,
            };

            if (data.page === 0) {
                // if new page, update time
                payload.time = time;
                dispatch({
                    type: 'search' + view,
                    payload: payload,
                    stack: stack,
                    reset: type === 'all',
                });
            } else {
                dispatch({ type: 'search_next' + view, payload: payload });
            }
        } catch (e) {
            console.log(e.message);
        }
    };
};

function intersect(a, b) {
    return [...new Set(a)].filter((x) => new Set(b).has(x));
}
