import axios from 'axios';
import Constants from 'expo-constants';
import { Platform } from 'react-native';
let baseURL;

// if (Platform.OS === 'web' && process.env.NODE_ENV === 'development') {
//baseURL = 'http://localhost:8000/proxy';
// } else {
baseURL = 'http://ezenciel.com:5000';
// }

// todo restore
const ezapi = axios.create({ baseURL });
let ip = null;
let email = null;
let platform = null;

// insert additional axios customizations here
ezapi.interceptors.request.use((config) => {
    if (config.method === 'post') {
        config.data.device_id = Constants.installationId;
        if (ip) {
            config.data.ip_address = ip;
        }

        if (email) {
            config.data.email = email;
        }

        if (platform) {
            config.data.platform = platform;
        }
    }

    return config;
});

export const setEmail = (input) => {
    email = input;
};

export const setPlatform = (input) => {
    platform = input;
};

export const getIPAddress = async () => {
    const { data } = await axios.get('https://api.ipify.org?format=json');

    ip = data.ip;

    return ip;
};

export default ezapi;
