import React, { PureComponent } from 'react';
import { StyleSheet, View, Linking, Platform } from 'react-native';
import { WebView } from 'react-native-webview';
import colors from '../../../constants/colors';
import fontSize from '../../../constants/fontSize';
import SafeArea from '../content/SafeArea';
import Loading from './Loading';

class WebViewModal extends PureComponent {
    static navigationOptions = ({ navigation }) => ({
        headerTitle: navigation.getParam('url').substr(0, 30) + '...',
        headerBackTitle: ' ',
    });

    constructor(props) {
        super(props);

        this.state = {
            uri: 'http://www.ezenciel.com/',
        };
    }

    static getDerivedStateFromProps(props, state) {
        const { navigation } = props;
        const url = navigation.getParam('url', 'http://www.ezenciel.com/');

        return {
            ...state,
            uri: url,
        };
    }

    renderLoading = () => (
        <View style={styles.activityContainer}>
            <Loading />
        </View>
    );

    getMeta = `
    setTimeout(function() {
      window.ReactNativeWebView.postMessage(document.title);
    }, 100)
  `;

    postMessage = (e) => {
        const { navigation } = this.props;
        const title = navigation.getParam('title', '');
        if (title) return;
        navigation.setParams({ title: e.nativeEvent.data });
    };

    render() {
        const { uri } = this.state;
        if (Platform.OS === 'web') {
            Linking.openURL(uri);
        }
        return (
            <SafeArea style={{ flex: 1, paddingTop: 0 }}>
                <WebView
                    startInLoadingState
                    renderLoading={this.renderLoading}
                    injectedJavaScript={this.getMeta}
                    onMessage={this.postMessage}
                    source={{ uri }}
                />
            </SafeArea>
        );
    }
}

const styles = StyleSheet.create({
    backButton: {
        width: '100%',
        height: 50,
        backgroundColor: colors.darkGray,
        zIndex: 10,
    },
    backButtonText: {
        color: colors.extraLightGray,
        fontSize: fontSize.big,
    },
    activity: {
        justifyContent: 'center',
        alignItems: 'center',
    },
    activityContainer: {
        flex: 1,
        alignItems: 'center',
        justifyContent: 'center',
        position: 'absolute',
        top: 0,
        bottom: 0,
        right: 0,
        left: 0,
        zIndex: 1,
        backgroundColor: 'white',
    },
});

export default WebViewModal;
