import React from 'react';
import { Text, View } from 'react-native';
import { Icon } from 'native-base';
import Highlighter from 'react-native-highlight-words';
import moment from 'moment';
import styles from './styles/Comments.style';

const Review = ({ item, selectedTags }) => {
  const { date, _id: id, text, title, rating } = item;
  const timePassed = moment(date).fromNow();
  const stars = [];
  for (let i = 1; i <= rating; i += 1) {
    stars.push(
      <Icon
        key={`${id}-${i}`}
        name="star"
        type="FontAwesome"
        style={[
          styles.iconStyle,
          {
            marginLeft: i === 1 ? 0 : 5
          }
        ]}
      />
    );
  }

  return (
    <View style={styles.commentContainer} key={`comment-${id}`}>
      <Text style={styles.date}>{timePassed}</Text>
      <View style={styles.ratingContainer}>{stars}</View>
      {title ? (
        <View style={styles.authorContainer}>
          <Text style={styles.author}>{title}</Text>
        </View>
      ) : null}

      <Highlighter
        autoEscape
        textToHighlight={text}
        style={styles.comment}
        searchWords={selectedTags}
        highlightStyle={styles.highlight}
      />
    </View>
  );
};

export default Review;
