import React, { useEffect, useState } from 'react';
import {
    FlatList,
    StyleSheet,
    Text,
    TouchableOpacity,
    View,
} from 'react-native';
import AppModal from '../app/views/AppModal';
import { useSelector } from 'react-redux';
import Loading from '../app/views/Loading';
import TextMessage from '../app/messages/TextMessage';
import i18n from '../../utils/i18n';
import { ListForm } from './ListForm';
import fontSize from '../../constants/fontSize';
import colors from '../../constants/colors';
import spacing from '../../constants/spacing';
import IconButton from '../app/buttons/IconButton';
import Spacer from '../app/other/Spacer';
import { withNavigation } from 'react-navigation';

export function sortbyDate(a, b) {
    return b.modified - a.modified;
}

const ListAddFlow = ({
    navigation,
    visible,
    type,
    item,
    onClose,
    onAdd,
    onDelete,
}) => {
    const [state, setState] = useState({ step: 0, sort: true, ordered: [] });
    const user = useSelector((state) => state.user);
    const lists = useSelector((state) => state.Lists);
    const data = user.isLoggedIn && lists ? Object.values(lists) : null;
    let others = data ? data.filter((x) => x.type === type) : [];

    if (state.sort && others.length > 0) {
        // sort by date
        others = others.sort(sortbyDate);

        // save order locally
        const ordered = others.map((x) => x.id);
        setState({ ...state, sort: false, ordered: ordered });
    }

    useEffect(() => {
        !visible ? setState({ ...state, sort: true }) : null;
    }, [visible]);

    // set in right order
    if (state && state.ordered && others.length > 0) {
        others = state.ordered.map((x) => lists[x]);
    }

    const empty = <TextMessage text={i18n.t('lists.noLists')} />;
    const type_name = type
        ? type.substring(0, 1).toUpperCase() +
          type.substring(1, type.length - 1)
        : '';
    others = others.map((x) => ({
        id: x.id,
        name: x.name,
        inList: !!x.items.find((x) => x._id === item._id),
        size: x.items.length,
    }));

    const onPress = (listInfo) => {
        if (listInfo.inList) {
            onDelete(listInfo.id, item);
        } else {
            onAdd(listInfo.id, item);
        }
    };

    const renderItem = ({ item }) => {
        return (
            <TouchableOpacity onPress={() => onPress(item)}>
                <View
                    style={[
                        styles.list,
                        item.inList
                            ? { backgroundColor: colors.extraLightGray }
                            : {},
                    ]}>
                    <View style={[styles.selection]}>
                        <View style={styles.text}>
                            <Text style={styles.name}>{item.name}</Text>
                            <Text style={styles.label}>{`${
                                item.size
                            } ${type_name}${item.size === 1 ? '' : 's'}`}</Text>
                        </View>
                        <IconButton
                            iconFont={item.inList ? 'Ionicons' : 'Ionicons'}
                            iconSize={20}
                            iconColor={
                                item.inList ? colors.green : colors.darkGray
                            }
                            onPress={() => onPress(item)}
                            iconName={
                                item.inList
                                    ? 'md-checkmark-circle-outline'
                                    : 'md-add'
                            }
                        />
                        <Spacer h={10} />
                    </View>
                </View>
            </TouchableOpacity>
        );
    };

    const close = () => {
        setState({ step: 0 });
        onClose();
    };

    const createNewList = () => {
        if (!user.isLoggedIn) {
            navigation.navigate('Auth');
            close();
        } else {
            setState({ step: 1 });
        }
    };

    return (
        <AppModal
            visible={visible}
            name={state.step === 0 ? `Add to ${type} list` : `New ${type} list`}
            onClose={close}
            actionText={state.step === 0 ? 'Create New List' : null}
            onSubmit={createNewList}>
            {state.step === 0 ? (
                // !data ? (
                //     <Loading />
                // ) : (
                <View style={styles.content}>
                    <FlatList
                        data={others}
                        keyExtractor={(item) => item.id}
                        ListEmptyComponent={empty}
                        renderItem={renderItem}
                        scrollEnabled
                        contain
                    />
                </View>
            ) : (
                //     )
                <ListForm
                    onSubmit={close}
                    user={user}
                    item={item}
                    listType={type}
                />
            )}
        </AppModal>
    );
};

const styles = StyleSheet.create({
    content: {
        padding: 10,
        paddingBottom: 0,
        maxHeight: '70%',
    },
    list: {
        padding: 10,
    },
    selection: {
        flexDirection: 'row',
        alignItems: 'center',
    },
    text: {
        flex: 1,
        padding: spacing.small,
        paddingTop: 0,
    },
    name: {
        fontFamily: 'OpenSansBold',
        fontSize: fontSize.medium,
        color: colors.extraDarkGray,
    },
    label: {
        fontFamily: 'OpenSans',
        fontSize: fontSize.extraSmall,
        color: colors.darkGray,
    },
});

export default withNavigation(ListAddFlow);
