import React from 'react';
//import Modal from 'react-native-modal';
import RenderModal from '../../app/views/RenderModal';
import { Platform, StyleSheet, Text, View } from 'react-native';
import spacing from '../../../constants/spacing';
import colors from '../../../constants/colors';
import fontSize from '../../../constants/fontSize';
import TextButton from '../buttons/TextButton';
import IconButton from '../buttons/IconButton';
import Spacer from '../other/Spacer';

const AppModal = ({
    visible,
    name,
    children,
    actionText,
    actionIcon,
    onClose,
    onSubmit,
}) => {
    const opt = {
        visible: visible,
        onBackButtonPress: onClose,
        onBackdropPress: onClose,
        style: styles.modal,
        avoidKeyboard: true,
    };
    return (
        <RenderModal options={opt}>
            <View style={styles.modalContentContainer}>
                <View style={{ flexDirection: 'row' }}>
                    <IconButton
                        iconName="close"
                        iconFont="MaterialIcons"
                        iconSize={20}
                        onPress={onClose}
                    />
                    <Spacer style={{ flex: 1 }} />
                    {!actionIcon ? null : actionIcon}
                </View>
                <Text style={styles.name}>{name}</Text>
                {children}
                {!actionText ? null : (
                    <View style={styles.actionsSection}>
                        <TextButton
                            style={styles.submitButton}
                            textStyle={styles.submitButtonText}
                            onPress={onSubmit}
                            text={actionText}
                        />
                    </View>
                )}
            </View>
        </RenderModal>
    );
};

const styles = StyleSheet.create({
    modal: {
        // justifyContent: 'flex-end',
        margin: 0,
        flex: 1,
        //backgroundColor: colors.white,
        display: 'flex',
        justifyContent: 'center',
        flexDirection: 'row',
        backgroundColor: '#555',
    },
    modalContentContainer: {
        maxHeight: 500,
        backgroundColor: 'white',
        borderTopStartRadius: spacing.medium,
        borderTopEndRadius: spacing.medium,
        padding: 15,
        paddingTop: 0,
        display: 'flex',
        justifyContent: 'center',
        margin: 0,
    },
    name: {
        fontFamily: 'Roboto_medium',
        fontSize: fontSize.huge,
        color: colors.darkGray,
        paddingTop: 0,
        paddingBottom: 10,
        textAlign: 'center',
    },
    actionsSection: {
        justifyContent: 'flex-end',
        flexDirection: 'row',
        borderTopColor: colors.lightGray,
        borderTopWidth: 1,
        paddingTop: 15,
        marginTop: 20,
        marginBottom: Platform.OS === 'ios' ? 10 : 5,
    },
    submitButton: {
        backgroundColor: colors.red,
        height: 35,
        borderRadius: 3,
        flex: 1,
    },
    submitButtonText: { color: colors.white, fontFamily: 'Roboto' },
});

export default AppModal;
