import React, { useEffect, useState } from 'react';
import {
    KeyboardAvoidingView,
    Platform,
    ScrollView,
    Text,
    TouchableOpacity,
    TouchableWithoutFeedback,
    View,
    Animated,
    Dimensions,
} from 'react-native';

import IconButton from '../app/buttons/IconButton';
import Icon from '../app/icons/Icon';
import colors from '../../constants/colors';
import fontSize from '../../constants/fontSize';
import Styles from '../app/other/Styles';
import SelectionButton from '../app/buttons/SelectionButton';
import Spacer from '../app/other/Spacer';
import SearchInput from './SearchInput';
import styles from './SearchBarStyle';
import constants from '../../constants/constants';
import FiltersModal from './FiltersModal';
import { useDispatch, useSelector } from 'react-redux';
import { search } from '../../store/search';
import { withNavigation } from 'react-navigation';
import ezapi from '../../utils/ezapi';
import RenderModal from '../app/views/RenderModal';
import FeedbackModal from '../screens/Feedback/FeedbackModal';

const SearchBar = ({ navigation, data, view = '' }) => {
    const dispatch = useDispatch();
    const Meta = useSelector((state) => state.Misc);
    const meta = Meta.skincare;
    const [state, setState] = useState({
        term: '',
        type: 'all',
        isActive: false,
        time: 0,
        suggestions: [],
        offset: new Animated.Value(0),
        feedbackVisible: false,
        visible: false,
    });

    const [suggestions, setSuggestions] = useState({
        search: [],
        time: 0,
    });

    // initial loading
    useEffect(() => {
        setState({
            ...state,
            term: data.last.term,
            type: data.last.type,
        });
        getSuggestions(data.last.term, Date.now());
    }, [data.last]);

    useEffect(() => {
        if (suggestions.time >= state.time) {
            setState({
                ...state,
                suggestions: suggestions.search,
            });
        }
    }, [suggestions]);

    async function getSuggestions(term, time) {
        if (term !== suggestions.term) {
            const { data: result } = await ezapi.post('/suggestions', {
                search: term,
                time: time,
            });
            if (result.search.length) {
                setSuggestions({
                    term: term,
                    search: result.search,
                    time: time,
                });
            }
        }
    }

    const onChangeText = async (input) => {
        const time = Date.now();
        setState({ ...state, term: input, time: time });
        getSuggestions(input, time);
    };

    const onClose = () => {
        setState({ ...state, isActive: false });
    };

    const goBack = () => {
        if (data.stack.length > 0) {
            dispatch({ type: 'search_back' + view });
        } else {
            navigation.navigate('Home');
        }
    };

    const endEditing = (input, stayActive = false) => {
        const { term, type } = input;
        const lastType = type ? type : state.type;
        const destinationView = lastType === 'all' ? '_home' : '';
        const lastTerm = destinationView === view ? state.term : data.last.term;
        // if new search while in product mode, remove product and stack if previous product
        let extra = null;
        if (term && term !== data.last.term && data.last.tagType) {
            extra = { tagType: null, tagName: null, product: [], author: [] };
        }

        setState({
            ...state,
            isActive: stayActive,
            ...input,
            ...extra,
            type: destinationView === view ? lastType : data.last.type, // stick always to its type
            term: lastTerm, // term, if term is transferred, stick to the previous one
            suggestions:
                destinationView !== view || !lastTerm ? [] : state.suggestions,
        });
        dispatch(
            search({
                params: {
                    ...data.last,
                    term: state.term,
                    ...input,
                    ...extra,
                    page: 0,
                },
                stack: data.last.type !== lastType || extra,
                view: destinationView,
            }),
        );

        // navigate if different type
        if (lastType === 'all' && view === '') {
            navigation.navigate('Home');
        } else if (lastType !== 'all' && view === '_home') {
            navigation.navigate('SearchMain', { stopLoading: true });
        }
    };

    const activate = () => {
        setState({ ...state, isActive: true });
    };

    const textItem = (item) => {
        return (
            <TouchableOpacity
                key={item}
                onPress={() => {
                    endEditing({ term: item });
                }}>
                <View style={{ flexDirection: 'row' }}>
                    <Text style={[Styles.text, styles.suggestion]}>{item}</Text>
                    <TouchableOpacity
                        onPress={() => {
                            onChangeText(item);
                        }}>
                        <View style={[styles.icon]}>
                            <Icon
                                name="arrow-up-left"
                                size={18}
                                color={colors.gray}
                                font="Feather"
                            />
                        </View>
                    </TouchableOpacity>
                </View>
            </TouchableOpacity>
        );
    };

    const content = (padding) => (
        <ScrollView
            style={{
                height: Dimensions.get('window').height - 150,
            }}
            keyboardShouldPersistTap√és="always"
            showsVerticalScrollIndicator={false}>
            {state.term !== '' && state.suggestions.length ? (
                <>
                    <Text style={[Styles.text, styles.sectionText]}>
                        SUGGESTED SEARCHES
                    </Text>
                    {state.suggestions.map(textItem)}
                </>
            ) : null}

            <Text style={[Styles.text, styles.sectionText]}>
                POPULAR SEARCHES
            </Text>
            {meta.search.popular.map(textItem)}

            <Text style={[Styles.text, styles.sectionText]}>
                TRENDING SEARCHES
            </Text>
            {meta.search.trending.map(textItem)}

            <Spacer v={padding} />
        </ScrollView>
    );
    const opt = {
        animationType: 'slide',
        transparent: false,
        visible: state.isActive,
    };

    const getIcon = (screen) => {
        switch (screen) {
            case 'Home':
                return 'home';
            case 'Search':
                return 'search';
            case 'Lists':
                return 'heart';
            case 'FeedBack':
                return 'message-circle';
            default:
                return 'home';
        }
    };

    const GetButton = (props) => {
        return (
            <IconButton
                iconName={getIcon(props.screen)}
                iconSize={20}
                iconColor={
                    navigation.state.routeName === props.stackNavigator
                        ? colors.default
                        : colors.semiGray
                }
                containerStyle={
                    navigation.state.routeName === props.stackNavigator
                        ? [styles.unselectedTab, styles.selectedTab]
                        : styles.unselectedTab
                }
                onPress={() => navigation.navigate(props.stackNavigator)}
            />
        );
    };
    const showFeedbackModal = () => {
        setState({ ...state, feedbackVisible: true });
    };

    const hideFeedbackModal = () => {
        setState({ ...state, feedbackVisible: false });
    };

    return (
        <>
            <RenderModal options={opt}>
                <View
                    style={{
                        flexDirection: 'row',
                        alignItems: 'center',
                        paddingLeft: 10,
                    }}>
                    <SearchInput
                        term={state.term}
                        endEditing={() => endEditing({ term: state.term })}
                        onChangeText={onChangeText}
                        resetBar={() => endEditing({ term: '' }, true)}
                    />
                    <TouchableWithoutFeedback onPress={onClose}>
                        <View>
                            <Text style={[Styles.text, styles.cancel]}>
                                Cancel
                            </Text>
                        </View>
                    </TouchableWithoutFeedback>
                </View>
                <View style={styles.suggestions}>
                    <Text
                        style={[
                            Styles.text,
                            styles.sectionText,
                            { color: colors.darkGray },
                        ]}>
                        SEARCH FOR
                    </Text>
                    <View style={{ flexDirection: 'row' }}>
                        <SelectionButton
                            text={'All'}
                            isSelected={data.last.type === constants.ALL}
                            onPress={() => endEditing({ type: constants.ALL })}
                        />
                        <Spacer h={10} />
                        <SelectionButton
                            text={'Media'}
                            isSelected={data.last.type === constants.MEDIA}
                            onPress={() =>
                                endEditing({ type: constants.MEDIA })
                            }
                        />
                        <Spacer h={10} />
                        <SelectionButton
                            text={'Influencers'}
                            isSelected={data.last.type === constants.AUTHORS}
                            onPress={() =>
                                endEditing({ type: constants.AUTHORS })
                            }
                        />
                        <Spacer h={10} />
                        <SelectionButton
                            text={'Products'}
                            isSelected={data.last.type === constants.PRODUCTS}
                            onPress={() =>
                                endEditing({ type: constants.PRODUCTS })
                            }
                        />
                    </View>
                    <Spacer v={15} />
                    {Platform.OS === 'ios' ? (
                        <KeyboardAvoidingView behavior="height">
                            {content(100)}
                        </KeyboardAvoidingView>
                    ) : (
                        content(250)
                    )}
                </View>
            </RenderModal>
            <View style={styles.containerMain}>
                <View style={styles.searchBar}>
                    {data.stack.length > 0 || state.type !== constants.ALL ? (
                        <TouchableOpacity onPress={goBack}>
                            <Icon
                                name="arrow-left"
                                size={20}
                                color={colors.darkGray}
                            />
                        </TouchableOpacity>
                    ) : (
                        <Icon
                            name="search"
                            size={fontSize.big}
                            color={colors.darkGray}
                            font="Feather"
                        />
                    )}
                    <TouchableWithoutFeedback onPress={activate}>
                        <View style={{ flex: 1 }}>
                            {!data.last.tagType ? (
                                <Text
                                    style={[
                                        styles.inputText,
                                        data.last.term === ''
                                            ? styles.placeholderText
                                            : {},
                                        { paddingRight: 10 },
                                    ]}
                                    numberOfLines={1}>
                                    {data.last.term === ''
                                        ? 'Search'
                                        : data.last.term}
                                </Text>
                            ) : (
                                <View style={styles.inputArea}>
                                    <Text style={styles.type}>
                                        {data.last.tagType}
                                    </Text>
                                    <View style={styles.tag}>
                                        <Text
                                            style={[styles.textTag]}
                                            numberOfLines={1}>
                                            {data.last.tagName}
                                        </Text>
                                    </View>
                                </View>
                            )}
                        </View>
                    </TouchableWithoutFeedback>

                    {(data.last.term && data.last.term !== '') ||
                    data.last.tagType ? (
                        <TouchableOpacity
                            onPress={() =>
                                endEditing({
                                    term: '',
                                    tagType: null,
                                    tagName: null,
                                    product: [],
                                    author: [],
                                })
                            }>
                            <View style={[styles.icon]}>
                                <Icon
                                    name="close"
                                    size={18}
                                    color={colors.darkGray}
                                    font="MaterialIcons"
                                />
                            </View>
                        </TouchableOpacity>
                    ) : null}
                </View>
                {Platform.OS === 'web' &&
                Dimensions.get('window').width > 500 ? (
                    <View
                        style={{
                            display: 'flex',
                            flexDirection: 'row',
                            marginLeft: 20,
                        }}>
                        <GetButton
                            stackNavigator={'HomeMain'}
                            screen={'Home'}
                        />

                        <GetButton
                            stackNavigator={'SearchMain'}
                            screen={'Search'}
                        />

                        <GetButton
                            stackNavigator={'ListsMain'}
                            screen={'Lists'}
                        />

                        <IconButton
                            iconName="message-circle"
                            iconSize={20}
                            iconColor={colors.semiGray}
                            onPress={showFeedbackModal}
                        />

                        <FeedbackModal
                            visible={state.feedbackVisible}
                            hideModal={hideFeedbackModal}
                        />
                    </View>
                ) : null}
                <View style={{ paddingLeft: 10, paddingRight: 10 }}>
                    <FiltersModal view={view} loadFilters searchData={data} />
                </View>
            </View>
        </>
    );
};

export default withNavigation(SearchBar);
