import React, {PureComponent} from 'react';
import {Image, StyleSheet, Text, TouchableWithoutFeedback, View} from 'react-native';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {withNavigation} from 'react-navigation';
import moment from 'moment';
import AuthorAvatar from '../author/AuthorAvatar';
import Video from './Video';
import RelatedItemsList from './RelatedItemsList';
import {openWebView} from '../../utils/navigator';
import colors from "../../constants/colors";
import spacing from "../../constants/spacing";
import fontSize from "../../constants/fontSize";

class MediaCard extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            imageError: false
        };
    }

    onLinkPress = productId => {
        const {navigation} = this.props;
        navigation.push('ProductDetails', {productId});
    };

    navigateToAuthor = (author) => {
        const {navigation} = this.props;
        navigation.navigate('Author', {author: author, fromRoute: navigation.state.routeName})
    };

    errorLoadingImage = () => {
        this.setState({imageError: true});
    };

    getTopMostLabel = () => {
        const {data} = this.props;
        const {products} = data;
        const {categories, subcategories, names} = products;
        let array = [];

        if (names && names.length) {
            array = names;
        }

        if (categories && categories.length) {
            array = categories;
        }

        if (subcategories && subcategories.length) {
            array = subcategories;
        }

        const string = array
            .slice(0, 2)
            .join(' & ')
            .toUpperCase();

        return array.length > 0 ? <Text style={styles.section}>{string}</Text> : null
    };

    render() {
        const {imageError} = this.state;
        const {data, hideImage, hideAuthor} = this.props;
        const {products, title, summary, image, link, author, date} = data;
        const dateTime = moment(date).fromNow();
        const isVideo = link && link.includes('youtube');

        return (
            <View style={styles.container}>
                <View style={styles.content}>
                    {this.getTopMostLabel()}

                    <Text style={styles.date}>{dateTime}</Text>

                    {author && !hideAuthor ? (
                        <TouchableWithoutFeedback
                            onPress={() => this.navigateToAuthor(author)}
                        >
                            <View style={{paddingTop: 5}}>
                                <AuthorAvatar data={author}/>
                            </View>
                        </TouchableWithoutFeedback>
                    ) : null}

                    <TouchableWithoutFeedback
                        onPress={() => openWebView(link, {title}, {type: 'blog', author: author._id, title: title})}>
                        <View>
                            <Text style={styles.title}>{title}</Text>
                        </View>
                    </TouchableWithoutFeedback>

                    <Text style={styles.description}>{summary}</Text>
                </View>

                {isVideo ? image.includes('live.jpg') ? null : (
                    <Video image={image} videoLink={link}/>
                ) : (
                    image &&
                    !hideImage &&
                    !imageError && (
                        <Image
                            style={styles.image}
                            source={{uri: image}}
                            onError={this.errorLoadingImage}
                        />
                    )
                )}

                <RelatedItemsList data={products.list} onLinkPress={this.onLinkPress}/>
                <View style={{backgroundColor: colors.lightGray, padding: 3}}/>
            </View>
        );
    }
}

const styles = StyleSheet.create({
    container: {
        backgroundColor: colors.white,
        marginBottom: spacing.small,
    },
    content: {
        padding: spacing.big,
        paddingTop: spacing.medium,
    },
    section: {
        fontFamily: 'OpenSansBold',
        fontSize: fontSize.small,
        color: colors.red
    },
    date: {
        fontSize: fontSize.extraSmall,
        fontFamily: 'Roboto',
        color: colors.categoriesLightGray,
        paddingTop: 1
    },
    title: {
        marginTop: spacing.medium,
        color: colors.darkGray,
        fontFamily: 'OpenSansBold',
        fontSize: fontSize.extraBig,
    },
    description: {
        marginTop: spacing.small,
        fontFamily: 'OpenSans',
        fontSize: fontSize.medium,
        color: colors.darkGray,
        lineHeight: 20,
    },
    image: {
        width: '100%',
        height: 300,
        marginTop: spacing.medium
    },
    relatedItemsList: {
        flexDirection: 'row',
        marginTop: 15
    },
    relatedItemContainer: {
        backgroundColor: colors.lightGray,
        padding: 5,
        flexDirection: 'row',
        alignContent: 'center',
        justifyContent: 'center',
        marginRight: 15
    },
    iconContainer: {
        flex: 1,
        marginLeft: 10,
        alignItems: 'center',
        justifyContent: 'center'
    },
    linkLabel: {
        fontSize: fontSize.semiMedium
    }
});

export default withNavigation(MediaCard);
