import React, { useState } from 'react';
import {
    Platform,
    Image,
    StyleSheet,
    Text,
    TextInput,
    TouchableOpacity,
    View,
    KeyboardAvoidingView,
} from 'react-native';
import { Formik } from 'formik';
import Button from '../app/buttons/TextButton';
import spacing from '../../constants/spacing';
import fontSize from '../../constants/fontSize';
import colors from '../../constants/colors';
import i18n from '../../utils/i18n';
import * as Yup from 'yup';
import Styles from '../app/other/Styles';
import GoogleLogin from 'react-google-login';

const LoginFormSchema = Yup.object().shape({
    email: Yup.string()
        .required(
            i18n.t('validation.required', { field: i18n.t('auth.email') }),
        )
        .email(i18n.t('validation.invalid', { field: i18n.t('auth.email') })),
    password: Yup.string().required(
        i18n.t('validation.required', { field: i18n.t('auth.password') }),
    ),
});

const RegistrationFormSchema = Yup.object().shape({
    email: Yup.string()
        .required(
            i18n.t('validation.required', { field: i18n.t('auth.email') }),
        )
        .email(i18n.t('validation.invalid', { field: i18n.t('auth.email') })),
    password: Yup.string()
        .required(
            i18n.t('validation.required', { field: i18n.t('auth.password') }),
        )
        .min(
            6,
            i18n.t('validation.minLength', {
                field: i18n.t('auth.password'),
                length: 6,
            }),
        ),
    confirmPassword: Yup.string()
        .oneOf([Yup.ref('password'), null], i18n.t('auth.matchPassword'))
        .required(
            i18n.t('validation.required', {
                field: i18n.t('auth.confirmPassword'),
            }),
        ),
});

const LoginForm = ({ SignIn, SignUp, Facebook, Google }) => {
    const initialValues = {};
    const [state, setState] = useState({ signin: true });

    const strings = {
        signIn: 'Sign in',
        signUp: 'Sign up',
        logOut: 'Log Out',
        continue: 'Continue',
        confirmPassword: 'confirm password',
        eMail: 'e-mail',
        password: 'password',
        logInToContinue: 'Sign in to continue',
        signuptoContinue: 'Sign up to continue',
        notYetRegistered: 'Not registered yet?',
        haveAnAccount: 'Do you have an account?',
    };

    return (
        <View style={styles.container}>
            <KeyboardAvoidingView behavior="position" style={styles.keyboard}>
                <View style={styles.content}>
                    <View style={styles.logoContainer}>
                        <Image
                            style={styles.logo}
                            resizeMode="contain"
                            source={require('../../../assets/logo-text.png')}
                        />
                    </View>
                    <View>
                        <Text style={styles.title}>
                            {state.signin
                                ? `${strings.logInToContinue}`
                                : `${strings.signuptoContinue}`}
                        </Text>
                        <View style={styles.signUpContainer}>
                            <Text style={styles.signUpTitle}>
                                {state.signin
                                    ? `${strings.notYetRegistered} `
                                    : `${strings.haveAnAccount} `}
                            </Text>
                            <TouchableOpacity
                                onPress={() =>
                                    setState({ signin: !state.signin })
                                }>
                                <Text style={styles.signUp}>
                                    {state.signin
                                        ? strings.signUp
                                        : strings.signIn}
                                </Text>
                            </TouchableOpacity>
                        </View>

                        <View style={styles.googleContainer}>
                            <TouchableOpacity onPress={Google}>
                                <Image
                                    style={styles.google}
                                    resizeMode="contain"
                                    source={require('../../../assets/google.png')}
                                />
                            </TouchableOpacity>
                        </View>
                        <View style={styles.facebookContainer}>
                            <TouchableOpacity onPress={Facebook}>
                                <Image
                                    style={styles.facebook}
                                    resizeMode="contain"
                                    source={require('../../../assets/facebook.png')}
                                />
                            </TouchableOpacity>
                        </View>
                        <Text style={styles.signUpTitle}> or </Text>
                        <Formik
                            initialValues={{
                                email: '',
                                password: '',
                                confirmPassword: '',
                            }}
                            onSubmit={state.signin ? SignIn : SignUp}
                            validationSchema={
                                state.signin
                                    ? LoginFormSchema
                                    : RegistrationFormSchema
                            }
                            validateOnBlur={false}
                            validateOnChange={false}>
                            {({
                                handleChange,
                                handleBlur,
                                handleSubmit,
                                values,
                                isValid,
                                errors,
                                dirty,
                            }) => (
                                <>
                                    <TextInput
                                        style={
                                            !errors.email
                                                ? styles.input
                                                : [
                                                      styles.input,
                                                      styles.inputInvalid,
                                                  ]
                                        }
                                        placeholder={i18n.t('auth.email')}
                                        onChangeText={handleChange('email')}
                                        onBlur={handleBlur('email')}
                                        value={values.email}
                                        keyboardType="email-address"
                                        autoCapitalize="none"
                                    />
                                    {errors.email ? (
                                        <Text style={Styles.error}>
                                            {errors.email}
                                        </Text>
                                    ) : null}
                                    <TextInput
                                        placeholder={i18n.t('auth.password')}
                                        style={
                                            !errors.password
                                                ? styles.input
                                                : [
                                                      styles.input,
                                                      styles.inputInvalid,
                                                  ]
                                        }
                                        onChangeText={handleChange('password')}
                                        onBlur={handleBlur('password')}
                                        value={values.password}
                                        secureTextEntry
                                        autoCapitalize="none"
                                    />
                                    {errors.password ? (
                                        <Text style={Styles.error}>
                                            {errors.password}
                                        </Text>
                                    ) : null}
                                    {state.signin ? null : (
                                        <TextInput
                                            placeholder={i18n.t(
                                                'auth.confirmPassword',
                                            )}
                                            style={
                                                !errors.confirmPassword
                                                    ? styles.input
                                                    : [
                                                          styles.input,
                                                          styles.inputInvalid,
                                                      ]
                                            }
                                            onChangeText={handleChange(
                                                'confirmPassword',
                                            )}
                                            onBlur={handleBlur(
                                                'confirmPassword',
                                            )}
                                            value={values.confirmPassword}
                                            secureTextEntry
                                        />
                                    )}
                                    {!state.signin && errors.confirmPassword ? (
                                        <Text style={Styles.error}>
                                            {errors.confirmPassword}
                                        </Text>
                                    ) : null}
                                    <View style={styles.buttonsContainer}>
                                        <Button
                                            text={i18n.t('auth.continue')}
                                            style={styles.button}
                                            textStyle={styles.buttonText}
                                            onPress={handleSubmit}
                                        />
                                    </View>
                                </>
                            )}
                        </Formik>
                    </View>
                </View>
            </KeyboardAvoidingView>
        </View>
    );
};

const styles = StyleSheet.create({
    container: {
        flex: 1,
        backgroundColor: 'white',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    keyboard: {
        flex: 1,
    },
    content: {
        paddingLeft: spacing.huge,
        paddingRight: spacing.huge,
    },
    logoContainer: {
        alignItems: 'center',
        justifyContent: 'center',
    },
    logo: {
        height: 15,
        marginBottom: 40,
    },
    title: {
        fontSize: fontSize.extraHuge,
        fontFamily: 'Roboto',
        color: colors.darkGray,
        fontWeight: 'bold',
    },
    signUpContainer: {
        flexDirection: 'row',
        marginBottom: spacing.medium,
    },
    signUpTitle: {
        fontFamily: 'OpenSans',
        fontSize: fontSize.big,
        color: colors.darkGray,
    },
    signUp: {
        fontFamily: 'OpenSans',
        fontSize: fontSize.big,
        color: colors.red,
    },
    input: {
        height: 50,
        borderRadius: spacing.medium,
        marginTop: spacing.medium,
        paddingLeft: spacing.extraBig,
        backgroundColor: colors.extraLightGray,
        color: colors.darkGray,
        fontWeight: 'bold',
        fontFamily: 'Roboto',
        fontSize: fontSize.medium,
    },
    inputInvalid: {
        borderColor: colors.red2,
    },
    button: {
        width: 100,
        height: 40,
        borderRadius: spacing.medium,
        backgroundColor: colors.gray,
    },
    buttonText: {
        color: colors.white,
        fontWeight: 'bold',
    },
    buttonsContainer: {
        flexDirection: 'row-reverse',
        marginTop: spacing.medium,
    },
    googleContainer: {
        marginTop: 100,
        width: 250,
    },
    facebookContainer: {
        paddingTop: 10,
        paddingBottom: 10,
    },
    facebook: {
        height: 40,
        width: 250,
    },
    google: {
        height: 62,
        width: 250,
    },
});

export default LoginForm;
