import React from 'react';
import {Image, StyleSheet, Text, View} from 'react-native';
import fontSize from '../../constants/fontSize';
import colors from '../../constants/colors';
import spacing from "../../constants/spacing";
import AuthorActionBox from "./AuthorActionBox";
import Spacer from "../app/other/Spacer";
import Icon from "../app/icons/Icon";

const AuthorAvatar = ({large, data}) => (
    <View style={styles.container}>
        <View style={[styles.imageContainer, large ? {paddingRight: 5} : {}]}>
            {data.image &&
            <Image resizeMode="cover" style={large ? styles.imageLarge : styles.image} source={{uri: data.image}}/>}
        </View>
        <View style={data.image ? styles.nameFollowersContainer : {}}>
            <View style={{flexDirection: 'row', alignItems: 'center', }}>
                <Text numberOfLines={2} style={large ? styles.nameLarge : styles.name}>{data.name}</Text>
                <Spacer h={5}/>
                {data.channelId ?
                    <Icon name='youtube' font='FontAwesome' size={fontSize.medium} color={colors.gray}/>
                    : null}
            </View>

            <Text style={large ? styles.followersLarge : styles.followers}>{data.text}</Text>
            {large ?
                <View style={{marginTop: spacing.medium}}>
                    <AuthorActionBox author={data} addModal hideDelete/>
                </View>
                : null}
        </View>
    </View>
);

export default AuthorAvatar;

const styles = StyleSheet.create({
    container: {
        flexDirection: 'row',
        paddingRight: spacing.big,
    },
    imageContainer: {
        alignItems: 'center',
        justifyContent: 'center',
        paddingRight: 0,
    },
    image: {
        width: 30,
        height: 30,
        borderRadius: 30,
        borderWidth: 1,
        borderColor: colors.lightGray
    },
    imageLarge: {
        width: 80,
        height: 80,
        borderRadius: 40,
        borderWidth: 2,
        borderColor: colors.lightGray,
    },
    nameFollowersContainer: {
        marginLeft: spacing.medium,
        flex: 1,
    },
    nameLarge: {
        color: colors.darkGray,
        fontWeight: 'bold',
        marginRight: spacing.small,
        fontSize: fontSize.h2,
    },
    followersLarge: {
        fontSize: fontSize.medium,
        fontFamily: 'Roboto',
        color: colors.gray
    },
    name: {
        color: colors.darkGray,
        fontWeight: 'bold',
        marginRight: spacing.small
    },
    followers: {
        fontSize: fontSize.extraSmall,
        color: colors.gray,
        fontFamily: 'Roboto',
    }
});
