import React, {useState} from 'react';
import {FlatList, StyleSheet, Text, TouchableWithoutFeedback, View, TouchableOpacity} from 'react-native';
import fontSize from '../../constants/fontSize';
import colors from '../../constants/colors';
import spacing from "../../constants/spacing";
import AuthorCard from "../author/AuthorCard";
import Icon from "../app/icons/Icon";
import Spacer from "../app/other/Spacer";
import Styles from "../app/other/Styles";
import IconButton from "../app/buttons/IconButton";
import {withNavigation} from "react-navigation";
import {initialParams, search} from "../../store/search";
import {useDispatch} from "react-redux";
import ProductCardList from "../products/ProductCardList";

const ListCard = ({navigation, list, vertical, editList}) => {
    const dispatch = useDispatch();
    const [expanded, setExpanded] = useState(vertical);
    const data = list.items;
    const renderItem = ({item}) => {
        switch (list.type) {
            case 'products':
                return <ProductCardList data={item} row={expanded} listId={list.id}/>
            default:
                return <AuthorCard data={item} row={expanded} listId={list.id} hideDelete={list.type === 'following'}/>
        }
    };

    const empty = (
        <View style={{justifyContent: 'center', flexDirection: 'row', flex: 1, padding: 15}}>
            <Text style={Styles.message}>Nothing here yet! Add {list.type} to start</Text>
        </View>
    );

    const gotoMedia = () => {
        const tagType = list.type === 'products' ? 'Products' : 'Influencers';
        const filter = list.type === 'products' ?
            {product: list.items.length ? list.items.map(x => x._id) : ['NOTVALID']} :
            {author: list.items.length ? list.items.map(x => x._id) : ['NOTVALID']};

        const params = list.type === 'following' ? {
            ...initialParams,
            following: list.items.length ? list.items.map(x => x._id) : ['NOTVALID'],
            type: 'media',
        } : {
            ...initialParams,
            ...filter,
            type: 'media',
            tagName: list.name,
            tagType: tagType,
        };

        dispatch(search({
            params: params,
            stack: true,
        }));
        navigation.navigate('Search')
    };

    return (
        <View style={styles.list}>
            <TouchableWithoutFeedback onPress={() => setExpanded(!expanded)}>
                <View>
                    <View style={{marginRight: spacing.big}}>
                        <View style={{flexDirection: 'row'}}>
                            <View style={{flex: 1}}>
                                <Text numberOfLines={1} style={styles.name}>{list.name}</Text>
                            </View>
                            <View style={{paddingTop: 4}}>
                                <Icon name={!expanded ? 'expand-more' : 'expand-less'} font='MaterialIcons'
                                    size={fontSize.h5}/>
                            </View>
                        </View>
                        {!expanded || !list.description ? null :
                            <Text numberOfLines={2} style={styles.description}>{list.description}</Text>
                        }
                        <View style={{flexDirection: 'row', marginLeft: spacing.big}}>
                            <Text style={styles.subtitle}>
                                {`Added ${list.items.length} ${list.type === 'products' ? 'product' : 'influencer'}${list.items.length === 1 ? '' : 's'} to the list. `}
                            </Text>
                            <TouchableOpacity onPress={gotoMedia}>
                                <Text style={[styles.subtitle, {color: colors.red}]}>See media</Text>
                            </TouchableOpacity>
                        </View>
                    </View>
                </View>
            </TouchableWithoutFeedback>
            {expanded ?
                <View style={expanded || data.length === 0 ? styles.carousel_vertical : styles.carousel}>
                    {data.length > 0 ?
                        <FlatList
                            listKey={list.id}
                            data={data}
                            keyExtractor={(item) => item._id}
                            renderItem={renderItem}
                            scrollEnabled
                            horizontal={!expanded}
                            showsHorizontalScrollIndicator={false}
                        /> : null}
                </View>
                : null}
            {data.length == 0 && expanded ? empty : null}
            <View style={{alignItems: 'flex-end', marginRight: 10}}>
                <View style={{flexDirection: 'row'}}>
                    <IconButton iconName='folder-video' iconFont='Entypo' iconSize={fontSize.extraBig}
                                iconColor={colors.gray} onPress={gotoMedia}/>
                    <Spacer h={10}/>
                    <IconButton iconName='edit' iconFont='MaterialIcons' iconSize={fontSize.extraBig}
                                iconColor={colors.gray} onPress={() => editList(list)}/>
                    {/*<Spacer h={5}/>*/}
                    {/*<IconButton iconName='share' iconFont='Entypo' iconSize={fontSize.extraBig} iconColor={colors.gray}*/}
                    {/*            onPress={() => {*/}
                    {/*            }}/>*/}
                </View>
            </View>
        </View>
    )
};

export default withNavigation(ListCard);

const styles = StyleSheet.create({
    list: {
        marginBottom: 10,
        marginTop: 10,
    },
    carousel: {
        backgroundColor: colors.lighterGray,
        flexDirection: 'row',
        marginTop: 5,
        marginLeft: -10,
        paddingLeft: 10,
        paddingTop: 5,
        paddingBottom: 5,
    },
    carousel_vertical: {
        paddingTop: 10,
        paddingBottom: 5,
        marginLeft: spacing.big,
        marginRight: spacing.big,
    },
    name: {
        fontFamily: 'OpenSansBold',
        fontSize: fontSize.big,
        color: colors.extraDarkGray,
        marginLeft: spacing.big,
        flex: 1
    },
    subtitle: {
        fontFamily: 'OpenSans',
        fontSize: fontSize.extraSmall,
        color: colors.darkGray,
        paddingTop: 3
    },
    description: {
        fontFamily: 'OpenSans',
        fontSize: fontSize.medium,
        color: colors.darkGray,
        marginLeft: spacing.big,
        paddingTop: 3,
        paddingBottom: 3
    },
});
