import ezencielAPI from '../../utils/ezapi';

export const getProductDetails = id => {
    return async (dispatch, getState) => {
        const {user} = getState();
        const payload = {
            product_id: id
        };

        if (user.email) {
            payload.email = user.email;
        }

        const {data} = await ezencielAPI.post('/product', payload);

        return data;
    };
};

export const getProductComments = (id, tags, page) => async () => {
    const payload = {
        product_id: id
    };

    const reviewTags = [];
    const ratings = [];

    tags.forEach(tag => {
        if (typeof tag === 'number') return ratings.push(tag);

        return reviewTags.push(tag);
    });

    if (page) payload.page = page;

    if (reviewTags.length) payload.tags = reviewTags;

    if (ratings.length) payload.rating = ratings;

    const {data} = await ezencielAPI.post('/reviews', payload);

    return data;
};