import React, { useState, useEffect } from 'react';
import { FlatList, StyleSheet, Text, View } from 'react-native';
import i18n from '../utils/i18n';
import Loading from '../components/app/views/Loading';
import { useDispatch, useSelector } from 'react-redux';
import MainHeader from '../components/app/content/MainHeader';
import Styles from '../components/app/other/Styles';
import ActionRedButton from '../components/app/buttons/ActionRedButton';
import ListCard from '../components/lists/ListCard';
import TextMessage from '../components/app/messages/TextMessage';
import { withNavigation } from 'react-navigation';
import { deleteList } from '../store/lists';
import { ListForm } from '../components/lists/ListForm';
import AppModal from '../components/app/views/AppModal';
import IconButton from '../components/app/buttons/IconButton';
import spacing from '../constants/spacing';
import TabButton from '../components/app/buttons/TabButton';
import fontSize from '../constants/fontSize';
import colors from '../constants/colors';
import RenderModal from '../components/app/views/RenderModal';
const ListsScreen = ({ navigation }) => {
    const dispatch = useDispatch();
    const lists = useSelector((state) => state.Lists);
    const user = useSelector((state) => state.user);
    const [state, setState] = useState({
        visible: false,
        list: null,
        tab: 'influencers',
        loading: true,
    });

    // Get lists and sort
    const dataLists = user.isLoggedIn && lists ? Object.values(lists) : null;
    const dataFollowing = dataLists
        ? dataLists.filter((x) => x.type === 'following')
        : [];
    const dataInfluencers = dataLists
        ? dataLists.filter((x) => x.type === 'influencers')
        : [];
    const dataProducts = dataLists
        ? dataLists.filter((x) => x.type === 'products')
        : [];
    const data =
        state.tab === 'influencers'
            ? [...dataFollowing, ...dataInfluencers]
            : dataProducts;

    // List create, edit and delete
    const handleCloseModalPress = () => setState({ ...state, visible: false });

    const editList = (list) => {
        if (!user.isLoggedIn) {
            navigation.navigate('Auth');
        } else {
            setState({ ...state, visible: true, list: list, user: user });
        }
    };

    const submitDeleteList = () => {
        dispatch(deleteList(state.list.id));
        setState({ ...state, visible: false });
    };

    const selectTag = (item) => {
        setState({ ...state, tab: item });
    };

    const renderItem = ({ item }) => {
        // TODO removed collapsable function given bug on changing tabs
        // Product carrousel design color background on collapsed
        return <ListCard list={item} vertical={true} editList={editList} />;
    };

    const empty = <TextMessage text={i18n.t('lists.noLists')} />;
    const deleteIcon = state.list ? (
        <IconButton
            iconName="delete"
            iconFont="MaterialIcons"
            iconSize={20}
            onPress={submitDeleteList}
        />
    ) : null;
    const opt = {
        visible: state.visible,
        animationType: 'slide',
        transparent: false,
        name: () => {
            state.list ? `Edit ${state.tab} list` : `New ${state.tab} list`;
        },
        actionIcon: deleteIcon,
    };
    const GetList = () => {
        if (user.isLoggedIn && !data) {
            return <Loading />;
        } else {
            return (
                <FlatList
                    data={data}
                    keyExtractor={(item) => item.id}
                    ListEmptyComponent={empty}
                    renderItem={renderItem}
                    scrollEnabled
                    contain
                    style={styles.content}
                    showsVerticalScrollIndicator={false}
                />
            );
        }
    };
    useEffect(() => {
        setTimeout(() => {
            setState({ ...state, loading: false });
        }, 500);
    }, []);

    return (
        <View style={Styles.container}>
            <RenderModal options={opt}>
                <View
                    style={{
                        width: 34,
                        marginTop: 5,
                        marginBottom: 20,
                        marginLeft: 10,
                    }}>
                    <IconButton
                        iconName="close"
                        iconFont="MaterialIcons"
                        iconSize={24}
                        onPress={handleCloseModalPress}
                    />
                </View>
                <ListForm
                    onSubmit={handleCloseModalPress}
                    list={state.list}
                    user={state.user}
                    listType={state.tab}
                />
            </RenderModal>

            <View style={[styles.subContainer, { marginLeft: 10 }]}>
                <MainHeader hideSymbol>
                    <View style={styles.headerContent}>
                        <Text style={styles.title}>Lists</Text>
                        <ActionRedButton
                            text="New List"
                            onPress={() => editList(null)}
                        />
                    </View>
                    <View style={styles.tabs}>
                        <TabButton
                            text={'Influencers'}
                            isSelected={state.tab === 'influencers'}
                            onPress={() => {
                                selectTag('influencers');
                            }}
                        />
                        <TabButton
                            text={'Products'}
                            isSelected={state.tab === 'products'}
                            onPress={() => {
                                selectTag('products');
                            }}
                        />
                    </View>
                </MainHeader>
                <GetList />
            </View>
        </View>
    );
};

ListsScreen.navigationOptions = {
    headerTransparent: true,
    title: null,
};

export default withNavigation(ListsScreen);

const styles = StyleSheet.create({
    headerContent: {
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
        marginLeft: -spacing.medium + spacing.big,
        marginRight: -spacing.medium + spacing.big,
    },
    subContainer: {
        flex: 1,
        width: 935,
        maxWidth: 935,
        paddingRight: 10,
        paddingTop: 0,
    },
    content: {
        marginBottom: 50,
    },
    tabs: {
        marginTop: 20,
        flexDirection: 'row',
        borderBottomWidth: 1,
        borderColor: colors.gray,
    },
    title: {
        fontFamily: 'OpenSansSemiBold',
        fontSize: fontSize.h4,
        color: colors.darkGray,
    },
});
