import {
  GET_PRODUCTS,
  GET_PRODUCT_DETAILS,
  GET_NEXT_PRODUCTS,
  GET_PRODUCT_BLOGS,
  GET_PRODUCT_VIDEOS,
  CLEAR_PRODUCTS,
  CLEAR_PRODUCT_DETAILS,
  GET_NEXT_PRODUCT_BLOGS,
  GET_NEXT_PRODUCT_VIDEOS,
  IS_PRODUCT_BLOGS_LAST,
  IS_PRODUCT_VIDEOS_LAST,
  CLEAR_V_B_LAST,
  MARK_PRODUCTS_AS_LIKED,
} from '../types';

const initialState = {
  products: [],
  lastvideoBlog: null,
  productDetails: null
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_PRODUCTS:
      return {
        ...state,
        products: action.payload
      };
    case GET_NEXT_PRODUCTS:
      return {
        ...state,
        products: action.payload ? [...state.products, ...action.payload] : state.products
      };
    case GET_PRODUCT_DETAILS:
      return {
        ...state,
        productDetails: action.payload
      };
    case GET_PRODUCT_BLOGS:
      return {
        ...state,
        productDetails: { ...state.productDetails, blogs: action.payload }
      };
    case GET_NEXT_PRODUCT_BLOGS:
      return {
        ...state,
        productDetails: {
          ...state.productDetails,
          blogs: action.payload
            ? [...state.productDetails.blogs, ...action.payload]
            : state.productDetails.blogs
        }
      };
    case IS_PRODUCT_BLOGS_LAST:
      return {
        ...state,
        lastvideoBlog: { ...state.lastvideoBlog, isBloglast: action.isBloglast }
      };
    case GET_PRODUCT_VIDEOS:
      return {
        ...state,
        productDetails: { ...state.productDetails, videos: action.payload }
      };
    case GET_NEXT_PRODUCT_VIDEOS:
      return {
        ...state,
        productDetails: {
          ...state.productDetails,
          videos: action.payload
            ? [...state.productDetails.videos, ...action.payload]
            : state.productDetails.videos
        }
      };
    case IS_PRODUCT_VIDEOS_LAST:
      return {
        ...state,
        lastvideoBlog: { ...state.lastvideoBlog, isVideolast: action.isVideolast }
      };
    case CLEAR_V_B_LAST:
      return {
        ...state,
        lastvideoBlog: null
      };
    case CLEAR_PRODUCTS:
      return {
        ...state,
        products: []
      };
    case CLEAR_PRODUCT_DETAILS:
      return {
        ...state,
        productDetails:
          state.productDetails && action.payload === state.productDetails._id
            ? state.productDetails
            : null
      };

    case MARK_PRODUCTS_AS_LIKED:
      return {
        ...state,
        products: state.products.map(product => {
          const liked = action.payload.find(p1 => p1 === product._id);
          if (liked) {
            return { ...product, liked: true };
          }

          return { ...product, liked: false };
        }),
      };

    default:
      return state;
  }
}
