import React from 'react';
import {TouchableOpacity, View, Text, StyleSheet} from 'react-native';
import Icon from '../app/icons/Icon';
import fontSize from '../../constants/fontSize';
import colors from "../../constants/colors";
import spacing from "../../constants/spacing";

const RelatedItem = ({item, onPress}) => {
    const {_id: id, brand} = item;
    let {name} = item;

    if (name && name.length > 20) {
        name = `${name.slice(0, 20)}...`;
    }

    const label = `${brand} - ${name}`;

    return (
        <TouchableOpacity key={id} onPress={() => onPress(id)}>
            <View style={styles.relatedItemContainer}>
                <Text style={styles.linkLabel}>{label}</Text>
                <View style={styles.iconContainer}>
                    <Icon name="link" size={fontSize.semiMedium}/>
                </View>
            </View>
        </TouchableOpacity>
    );
};

const styles = StyleSheet.create({
    relatedItemContainer: {
        backgroundColor: colors.lightGray,
        padding: 5,
        flexDirection: 'row',
        alignContent: 'center',
        justifyContent: 'center',
        marginLeft: 10,
        marginBottom: spacing.big
    },
    iconContainer: {
        flex: 1,
        marginLeft: 10,
        alignItems: 'center',
        justifyContent: 'center'
    },
    linkLabel: {
        fontSize: fontSize.semiMedium
    }
});


export default RelatedItem;
