import React, {PureComponent} from 'react';
import {FlatList, StyleSheet, View} from 'react-native';
import TextButton from '../../app/buttons/TextButton';
import colors from "../../../constants/colors";
import spacing from "../../../constants/spacing";
import fontSize from "../../../constants/fontSize";

class TabsHorizontalList extends PureComponent {
    componentDidUpdate(prevProps) {
        const {activeTab} = this.props;
        if (prevProps.activeTab !== activeTab && this.flatList) {
            this.scrollToIndex(activeTab);
        }
    }

    scrollToIndex = index => {
        const {data} = this.props;
        if (!this.flatList) return false;
        if (index <= 0 || index >= data.length - 1) return false;

        return this.flatList.scrollToIndex({
            animated: true,
            index,
            viewPosition: 0.5
        });
    };

    itemClicked = (item, index) => {
        const {selectedChanged} = this.props;

        this.scrollToIndex(index);
        selectedChanged(index);
    };

    renderItem = ({item, index}) => {
        const {activeTab, data} = this.props;
        const isSelected = data[activeTab] === item.toString();

        return (
            <View style={styles.tabContainerStyle}>
                <TextButton
                    text={item}
                    style={
                        isSelected
                            ? [styles.unselectedItemStyle, styles.selectedItemStyle]
                            : styles.unselectedItemStyle
                    }
                    textStyle={
                        isSelected
                            ? [styles.unselectedTextStyle, styles.selectedTextStyle]
                            : styles.unselectedTextStyle
                    }
                    onPress={() => this.itemClicked(item, index)}
                />
                {isSelected && <View style={styles.bottomline}/>}
            </View>
        );
    };

    addFlatListRef = ref => {
        this.flatList = ref;
    };

    keyExtractor = item => item.toString();

    render() {
        const {style, data, activeTab} = this.props;

        return (
            <View style={[styles.container, style]}>
                <FlatList
                    ref={this.addFlatListRef}
                    contentContainerStyle={styles.listItemsContainer}
                    horizontal
                    data={data}
                    extraData={activeTab}
                    showsHorizontalScrollIndicator={false}
                    renderItem={this.renderItem}
                    keyExtractor={this.keyExtractor}
                />
            </View>
        );
    }
}

const styles = StyleSheet.create({
    container: {
        height: 40,
        alignItems: 'flex-start',
        justifyContent: 'flex-start',
        backgroundColor: colors.white
    },
    listItemsContainer: {
        justifyContent: 'flex-start',
        alignContent: 'flex-start',
        alignItems: 'center',
        // paddingHorizontal: spacing.extraBig,
        flex: 1
    },
    tabViewContainer: {flex: 1, paddingBottom: 50},
    unselectedItemStyle: {
        backgroundColor: colors.white,
        paddingHorizontal: spacing.big,
        height: 38
    },
    selectedItemStyle: {
        backgroundColor: colors.white
    },
    unselectedTextStyle: {
        color: colors.categoriesLightGray,
        fontFamily: 'OpenSansSemiBold',
        fontSize: fontSize.semiMedium
    },
    selectedTextStyle: {
        color: colors.categoriesGray,
        fontWeight: 'bold'
    },
    bottomline: {
        backgroundColor: colors.categoriesGray,
        height: 2
    },
    tabContainerStyle: {
        justifyContent: 'flex-start',
        backgroundColor: 'white',
        maxHeight: 40
    }
});

export default TabsHorizontalList;
