import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
    Platform,
    ScrollView,
    StyleSheet,
    Text,
    TouchableOpacity,
    View,
    Modal,
    Dimensions,
} from 'react-native';
import IconButton from '../app/buttons/IconButton';
import spacing from '../../constants/spacing';
import Constants from 'expo-constants';
import colors from '../../constants/colors';
import fontSize from '../../constants/fontSize';
import TextButton from '../app/buttons/TextButton';
import Spacer from '../app/other/Spacer';
import CheckBox from '../forms/CheckBox';
import LabelledSwitch from '../forms/LabelledSwitch';
import Icon from '../app/icons/Icon';
import { withBadge } from 'react-native-elements';
import { search } from '../../store/search';
import SelectionButton from '../app/buttons/SelectionButton';
import ModalWeb from 'modal-react-native-web';
import RenderModal from '../app/views/RenderModal';
import Styles from '../app/other/Styles';

const FiltersModal = ({
    loadFilters,
    view = '',
    fromRoute,
    searchData,
    tags,
}) => {
    const dispatch = useDispatch();
    const Meta = useSelector((state) => state.Misc);
    const data = useSelector((state) =>
        searchData
            ? searchData
            : fromRoute === 'SearchMain'
            ? state.Search
            : state.HomeSearch,
    );
    const searchAuthor = useSelector((state) => state.AuthorViewSearch);
    const lists = useSelector((state) => state.Lists);
    const following = lists
        ? Object.values(lists).find((x) => x.type === 'following')
        : null;
    const meta = Meta.skincare;
    const initial = {
        visible: false,
        modified: false,
        following: false,
        term: '',
        tags: [],
        type: 'media',
        product: [],
        tagName: null,
        tagType: null,
    };
    const [state, setState] = useState(initial);
    const isAuthorView = view === '_author';
    const count = () => {
        const following = !isAuthorView && state.following ? 1 : 0;
        const term = isAuthorView && state.term ? 1 : 0;
        const tagType = isAuthorView && state.tagType ? 1 : 0;
        return state.tags.length + following + term + tagType;
    };

    // Load from previous screen existing filters into state
    useEffect(() => {
        if (loadFilters) {
            setState({
                ...state,
                ...data.last,
                tags: tags ? tags : data.last.tags,
                following: data.last.following.length > 0,
            });
        }
    }, [data.last]);

    // Load from Author view
    useEffect(() => {
        if (isAuthorView) {
            setState({
                ...state,
                ...searchAuthor,
            });
        }
    }, [searchAuthor]);

    const setVisible = () => {
        setState({ ...state, visible: !state.visible });
    };

    const onSelected = (item) => {
        let tags = [];
        if (state.tags.find((x) => x._id === item._id)) {
            tags = state.tags.filter((x) => x._id !== item._id);
        } else {
            tags = [...state.tags, item];
        }
        setState({ ...state, tags: tags, modified: true });
    };

    const onSelectedTerm = () => {
        setState({
            ...state,
            modified: true,
            term: state.term ? null : data.last.term,
        });
    };

    const onSelectedFollowing = (item) => {
        setState({ ...state, modified: true, following: !state.following });
    };

    const SelectClear = () => {
        if (count() > 0) {
            setState({
                ...initial,
                visible: true,
                modified: true,
                following: false,
            });
        } else {
            const tags = [...meta.labels, ...meta.categories, ...meta.content];
            const term = isAuthorView ? data.last.term : null;
            const product = isAuthorView
                ? {
                      product: searchAuthor.product,
                      tagType: searchAuthor.tagType,
                      tagName: searchAuthor.tagName,
                  }
                : {};

            setState({
                ...state,
                tags: tags,
                term: term,
                modified: true,
                ...product,
            });
        }
    };

    const onSelectedProductTag = () => {
        setState({
            ...state,
            modified: true,
            tagType: state.tagType ? null : searchAuthor.tagType,
            tagName: state.tagType ? null : searchAuthor.tagName,
            product: state.tagType ? [] : searchAuthor.product,
        });
    };

    const Submit = ({ inputType }) => {
        const following_extra =
            !isAuthorView && following
                ? state.following
                    ? { following: following.items.map((x) => x._id) }
                    : { following: [] }
                : {};

        // when selecting media type
        const typeInfo = {
            product: inputType === 'products' ? [] : state.product,
            tagType: inputType === 'products' ? null : state.tagType,
            tagName: inputType === 'products' ? null : state.tagName,
            type: inputType ? inputType : state.type,
        };

        if (state.modified || inputType) {
            const last = !isAuthorView ? data.last : searchAuthor;
            dispatch(
                search({
                    params: {
                        ...last,
                        tags: state.tags,
                        term: state.term,
                        ...following_extra,
                        ...typeInfo,
                        page: 0,
                    },
                    stack: false,
                    view: view,
                }),
            );
            setState({
                ...state,
                modified: false,
                visible: false,
                ...typeInfo,
            });
        } else {
            setState({ ...state, visible: false });
        }
    };

    const section = ({ title, description, divisor }, children) => {
        return (
            <>
                <Text style={styles.section}>{title}</Text>
                {description ? (
                    <Text style={styles.description}>{description}</Text>
                ) : null}
                <Spacer v={10} />
                {children}
                <Spacer v={20} l={divisor} />
                <Spacer v={15} />
            </>
        );
    };

    const checkBox = (item) => {
        return (
            <CheckBox
                key={item._id}
                item={item}
                text={item.name}
                selected={state.tags.find((x) => x._id === item._id)}
                onSelected={onSelected}
            />
        );
    };

    const labels = () => {
        return meta.labels.map(checkBox);
    };

    const categories = () => {
        return meta.categories.map(checkBox);
    };

    const content = () => {
        return meta.content.map(checkBox);
    };

    const MessagesBadge = withBadge(count(), {
        left: 5,
        textStyle: { fontSize: 10 },
    })(Icon);

    const SearchType = () => {
        return (
            <View style={{ flexDirection: 'row' }}>
                <SelectionButton
                    text={'Media'}
                    isSelected={searchAuthor.type !== 'products'}
                    onPress={() => Submit({ inputType: 'media' })}
                />
                <Spacer h={10} />
                <SelectionButton
                    text={'Products'}
                    isSelected={searchAuthor.type === 'products'}
                    onPress={() => Submit({ inputType: 'products' })}
                />
            </View>
        );
    };
    const opt = {
        visible: state.visible,
        animationType: 'fade',
    };
    return (
        <>
            <RenderModal options={opt}>
                <View style={{ marginLeft: 6 }}>
                    <IconButton
                        iconName="close"
                        iconFont="MaterialIcons"
                        iconSize={24}
                        onPress={Submit}
                    />
                </View>
                <ScrollView
                    style={{
                        height: Dimensions.get('window').height - 150,
                    }}
                    showsVerticalScrollIndicator={false}>
                    <View style={styles.content}>
                        {isAuthorView
                            ? section(
                                  {
                                      title: 'Search for',
                                      description:
                                          'See their media or the mentioned products',
                                      divisor: true,
                                  },
                                  SearchType(),
                              )
                            : null}

                        {isAuthorView && data.last.term
                            ? section(
                                  {
                                      title: 'Search',
                                      description:
                                          'Search your term within their articles, blogs and youtube videos',
                                      divisor: true,
                                  },
                                  <CheckBox
                                      text={data.last.term}
                                      selected={state.term}
                                      style={styles.term}
                                      onSelected={onSelectedTerm}
                                  />,
                              )
                            : null}

                        {!isAuthorView && following
                            ? section(
                                  {
                                      title: 'Following List',
                                      description:
                                          'See results only from the influencers you are following',
                                      divisor: true,
                                  },
                                  <LabelledSwitch
                                      text={'Following Influencers'}
                                      selected={state.following}
                                      onSelected={onSelectedFollowing}
                                  />,
                              )
                            : null}

                        {isAuthorView && searchAuthor.tagType
                            ? section(
                                  {
                                      title: searchAuthor.tagType,
                                      description: null,
                                      divisor: true,
                                  },
                                  <CheckBox
                                      text={searchAuthor.tagName}
                                      selected={state.tagType}
                                      style={styles.term}
                                      onSelected={onSelectedProductTag}
                                  />,
                              )
                            : null}

                        {section(
                            {
                                title: 'Media categories',
                                description:
                                    'Youtube videos, blogs and articles are classified based on some common criteria. This limits the results to those selected',
                                divisor: true,
                            },
                            labels(),
                        )}

                        {section(
                            {
                                title: 'Product categories',
                                description:
                                    'Select whether you want to filter your results based on the mentioned product categories',
                                divisor: true,
                            },
                            categories(),
                        )}

                        {section(
                            {
                                title: 'Content type',
                                description:
                                    'Select whether you want to filter specifically by videos or articles',
                                divisor: false,
                            },
                            content(),
                        )}
                    </View>
                </ScrollView>
                <View style={styles.actionsSection}>
                    <Spacer h={10} />
                    <TextButton
                        style={[styles.button, styles.clearButton]}
                        textStyle={styles.clearButtonText}
                        onPress={SelectClear}
                        text={count() > 0 ? 'Clear All' : 'Select All'}
                    />
                    <Spacer f={0.5} />
                    <TextButton
                        style={[styles.button, styles.submitButton]}
                        textStyle={styles.submitButtonText}
                        onPress={Submit}
                        text={'Search'}
                    />
                    <Spacer h={10} />
                </View>
            </RenderModal>
            <TouchableOpacity onPress={setVisible}>
                {count() > 0 ? (
                    <MessagesBadge
                        font="Feather"
                        name="menu"
                        size={24}
                        color={colors.darkGray}
                        wrapperStyle={{
                            fontSize: fontSize.extraSmall,
                            alignItems: 'center',
                        }}
                    />
                ) : (
                    <Icon
                        name="menu"
                        size={24}
                        color={colors.darkGray}
                        font="Feather"
                    />
                )}
            </TouchableOpacity>
        </>
    );
};

const styles = StyleSheet.create({
    container: {
        padding: spacing.medium,
        paddingTop: Platform.OS === 'ios' ? Constants.statusBarHeight : 15,
        flex: 1,
        display: 'flex',
        justifyContent: 'center',
        flexDirection: 'row',
    },
    content: {
        flex: 1,
        paddingTop: 10,
        padding: spacing.medium,
        paddingBottom: 0,
    },
    section: {
        fontFamily: 'OpenSansSemiBold',
        fontSize: fontSize.h5,
        color: colors.extraDarkGray,
    },
    term: {
        fontFamily: 'OpenSans',
        fontSize: fontSize.h5,
        color: colors.gray,
    },
    other: {
        fontFamily: 'OpenSans',
        fontSize: fontSize.h6,
        color: colors.gray,
    },
    description: {
        fontFamily: 'OpenSans',
        fontSize: fontSize.body,
        color: colors.darkGray,
        lineHeight: 25,
        paddingTop: 5,
    },
    actionsSection: {
        flexDirection: 'row',
        borderTopColor: colors.lightGray,
        borderTopWidth: 1,
        paddingTop: 10,
        marginBottom: Platform.OS === 'ios' ? 10 : 16,
    },
    button: {
        height: 40,
        borderRadius: 5,
        flex: 1,
    },
    submitButton: {
        backgroundColor: colors.extraDarkGray,
    },
    clearButton: {
        borderWidth: 0.5,
        borderColor: colors.darkGray,
    },
    submitButtonText: {
        color: colors.white,
        fontFamily: 'OpenSans',
    },
    clearButtonText: {
        color: colors.darkGray,
        fontFamily: 'OpenSans',
    },
});

export default FiltersModal;
