import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { StyleSheet, Text, View } from 'react-native';
import i18n from '../utils/i18n';
import fontSize from '../constants/fontSize';
import colors from '../constants/colors';
import constants from '../constants/constants';
import StickyHeaderView from '../components/app/views/StickyHeaderView';
import MainHeader from '../components/app/content/MainHeader';
import SearchBar from '../components/search/SearchBar';
import MediaCard from '../components/media/MediaCard';
import { initialParams, search } from '../store/search';
import ProductCard from '../components/products/ProductCard';
import spacing from '../constants/spacing';
import ListAddFlow from '../components/lists/ListAddFlow';
import { addItemList, removeItemList } from '../store/lists';
import AuthorCardSearch from '../components/author/AuthorCardSearch';

const styles = StyleSheet.create({
    refreshContainer: {
        flex: 1,
        alignItems: 'center',
        justifyContent: 'center',
        backgroundColor: 'white',
    },
    listEmptyMessage: {
        flex: 1,
        alignItems: 'center',
        backgroundColor: 'white',
        paddingTop: 20,
    },
    listEmptyMessageText: {
        fontSize: fontSize.medium,
        color: colors.darkGray,
    },
    authorCard: {
        backgroundColor: 'white',
        paddingTop: spacing.big,
        paddingLeft: spacing.big,
        paddingRight: spacing.big,
        marginTop: 0,
        marginBottom: 4,
        marginLeft: 0,
        marginRight: 0,
        paddingBottom: spacing.big,
    },
    productCard: {
        backgroundColor: 'white',
        paddingTop: spacing.big,
        paddingLeft: spacing.medium,
        paddingRight: spacing.medium,
        marginTop: 0,
        marginBottom: 4,
        marginLeft: 0,
        marginRight: 0,
        paddingBottom: spacing.big,
    },
});

const SearchScreen = ({ navigation }) => {
    const dispatch = useDispatch();
    const [state, setState] = useState({ visible: false });
    const stopLoading = navigation.getParam('stopLoading');
    const data = useSelector((state) => state.Search);
    const Meta = useSelector((state) => state.Misc);
    const lists = useSelector((state) => state.Lists);
    let reviewsTag = [];
    try {
        reviewsTag = Meta.skincare.labels.filter((x) => x._id === 'reviews');
    } catch (e) {}

    const allLists = lists ? Object.values(lists) : [];
    const products = allLists.filter((x) => x.type === 'products');
    const influencers = allLists.filter((x) => x.type === 'influencers');

    const onProductLike = (item) => {
        setState({ ...state, visible: true, item: item, type: 'products' });
    };

    const onAuthorLike = (item) => {
        setState({ ...state, visible: true, item: item, type: 'influencers' });
    };

    const addToList = (listId, item) => {
        dispatch(addItemList(listId, item));
    };

    const removeFromList = (listId, item) => {
        dispatch(removeItemList(listId, item));
    };

    useEffect(() => {
        if (!data.last.loading && data.last.time === 0 && !stopLoading) {
            dispatch(search({ params: initialParams }));
        }
    }, []);

    const loadMore = () => {
        if (!data.last.isLast && !data.loading) {
            dispatch(
                search({
                    params: {
                        ...data.last,
                        page: data.last.page + 1,
                    },
                }),
            );
        }
    };

    const seeProductMedia = (product) => {
        const name = product.name + ' by ' + product.brand;
        dispatch(
            search({
                params: {
                    ...initialParams,
                    product: [product._id],
                    tags: reviewsTag,
                    type: 'media',
                    tagName: name,
                    tagType: 'Product',
                },
                stack: true,
            }),
        );
    };

    const empty = (
        <View style={styles.listEmptyMessage}>
            <Text
                style={{
                    ...styles.listEmptyMessageText,
                    ...styles.listEmptyMessage,
                }}>
                {i18n.t('emptyMedia')}
            </Text>
        </View>
    );

    const renderMedia = ({ item }) => <MediaCard data={item} />;

    const renderAuthor = ({ item }) => {
        const caption = getCaption(item);
        const inList = influencers.find((x) =>
            x.items.find((y) => y._id === item._id),
        );
        return (
            <>
                <View style={styles.authorCard}>
                    <AuthorCardSearch
                        author={item}
                        caption={caption}
                        inList={inList}
                        onLike={onAuthorLike}
                    />
                </View>
                <View
                    style={{ backgroundColor: colors.lightGray, padding: 2 }}
                />
            </>
        );
    };

    const renderProduct = ({ item }) => {
        const caption = getCaption(item);
        const inList = products.find((x) =>
            x.items.find((y) => y._id === item._id),
        );
        return (
            <>
                <View style={styles.productCard}>
                    <ProductCard
                        product={item}
                        caption={caption}
                        seeMedia={seeProductMedia}
                        inList={inList}
                        onLike={onProductLike}
                    />
                </View>
                <View
                    style={{ backgroundColor: colors.lightGray, padding: 2 }}
                />
            </>
        );
    };

    function getCaption(item) {
        const articles_count = item.count - item.videos;
        const videos =
            item.videos > 0
                ? `${item.videos} Youtube video${item.videos > 1 ? 's' : ''}`
                : '';
        const articles =
            articles_count > 0
                ? `${articles_count} Article${articles_count > 1 ? 's' : ''}`
                : '';
        const and = item.videos > 0 && articles_count > 0 ? ' & ' : '';
        const extra =
            data.last.term && data.last.term !== ''
                ? ' on ' + data.last.term
                : '';

        return { full: videos + and + articles + extra, extra: extra };
    }

    const renderItem = (input) => {
        switch (data.last.type) {
            case constants.MEDIA:
                return renderMedia(input);
            case constants.AUTHORS:
                return renderAuthor(input);
            case constants.PRODUCTS:
                return renderProduct(input);
        }
    };

    return (
        <StickyHeaderView
            data={data.last.items}
            empty={empty}
            loadMore={loadMore}
            loading={data.loading}
            renderItem={renderItem}
            resetScroll={data.last.time}
            page={data.last.page}>
            <MainHeader hideSymbol>
                <SearchBar data={data} view={'_search'} />
                {state.visible ? (
                    <ListAddFlow
                        visible={state.visible}
                        item={state.item}
                        type={state.type}
                        onClose={() => setState({ visible: false })}
                        onAdd={addToList}
                        onDelete={removeFromList}
                    />
                ) : null}
            </MainHeader>
        </StickyHeaderView>
    );
};

SearchScreen.navigationOptions = {
    headerTransparent: true,
    title: null,
};

export default SearchScreen;
