import React from 'react';
import {StyleSheet, View} from 'react-native';
import IconTextButton from "../app/buttons/IconTextButton";
import fontSize from "../../constants/fontSize";
import colors from "../../constants/colors";
import {openWebView} from '../../utils/navigator';
import TextCollapsable from "../app/content/TextCollapsable";

const AuthorExtraInfo = ({author}) => {
    function getIcon(type, profile) {
        return <IconTextButton
            iconName={type}
            iconSize={fontSize.extraBig}
            iconColor={colors.default}
            iconFont='FontAwesome'
            containerStyle={styles.socialIcon}
            text={profile.text}
            onPress={() => openWebView(profile.link, {}, {type: type, _id: profile._id})}
        />
    }

    return (
        author ?
            <>
                <View style={styles.description}>
                    <TextCollapsable text={author.description} style={styles.descriptionText}/>
                </View>
                <View style={[styles.socialIcons]}>
                    {author.instagram ? getIcon('instagram', author.instagram) : null}
                    {author.youtube ? getIcon('youtube', author.youtube) : null}
                    {author.twitter ? getIcon('twitter', author.twitter) : null}
                    {author.domainInfo ? getIcon('globe', author.domainInfo) : null}
                </View>
            </> : null
    )
};

const styles = StyleSheet.create({
    socialIcons: {
        flexDirection: 'row',
    },
    socialIcon: {
        backgroundColor: colors.extraLightGray,
        marginRight: 10
    },
    description: {
        paddingBottom: 7,
    },
    descriptionText: {
        fontSize: fontSize.medium,
        color: colors.darkGray
    }
});

export default AuthorExtraInfo;