import React from 'react';
import { View, Text, StyleSheet } from 'react-native';
import Score from './Score';
import spacing from '../../../constants/spacing';
import fontSize from '../../../constants/fontSize';
import colors from '../../../constants/colors';

// Name Score component
// pass name, score props to define texts
// pass nameStyle, scoreStyle to style texts
// pass style prop to style it's container
const styles = StyleSheet.create({
  container: {
    // flex: 1,
    flexDirection: 'row'
    // alignItems: 'center',
    // justifyContent: 'space-between',
  },
  name: {
    // flex: 1,
    width: '85%',
    paddingRight: spacing.medium,
    fontFamily: 'OpenSansSemiBold',
    fontSize: fontSize.big,
    color: colors.extraDarkGray
    // fontWeight: '500'
  }
});

const NameScore = ({
  style,
  nameStyle,
  numberOfLines,
  name,
  score,
  scoreStyle,
  scoreRadius
}) => (
  <View style={[styles.container, style]}>
    <Text style={[styles.name, nameStyle]} numberOfLines={numberOfLines}>
      {name}
    </Text>

    {score && typeof score === 'number' && (
      <Score scoreStyle={scoreStyle} percent={score} radius={scoreRadius} />
    )}
  </View>
);

export default NameScore;
