import React, { PureComponent } from 'react';
import { Animated, Platform, StyleSheet, Text, View } from 'react-native';
import { AppLoading } from 'expo';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { checkSession as checkSessionAction } from '../store/actions/user_actions';
import { cacheFonts, cacheImages } from '../utils/functions';
import { getIPAddress, setEmail, setPlatform } from '../utils/ezapi';
import spacing from '../constants/spacing';
import colors from '../constants/colors';
import fontSize from '../constants/fontSize';
import { getUserLists as getUserListsAction } from '../store/lists';
import { getMeta } from '../store/misc';

class SplashScreen extends PureComponent {
    constructor(props) {
        super(props);

        this.rotateValueHolder = new Animated.Value(0);
        this.state = {
            initializing: true,
            error: null,
        };
    }

    postInitialization = async () => {
        const { navigation, user } = this.props;

        // set platform
        setPlatform(Platform.OS);

        // Download user data if logged in
        if (user.isLoggedIn) {
            try {
                const { getUserLists } = this.props;

                // set email on API requests
                setEmail(user.email);

                // get lists
                getUserLists(user.uid);
            } catch (e) {
                console.log(e.message);
            }
        }
        this.setState({ initializing: false });
        return navigation.navigate('Root');
    };

    getInitialData = async () => {
        const { getMeta } = this.props;
        await getMeta();
    };

    getUserData = async () => {
        try {
            const { checkSession } = this.props;
            await checkSession();
        } catch (e) {
            console.log(e.message);
        }
    };

    loadAssetsAsync = async () => {
        const imageAssets = cacheImages([
            require('../../assets/logo-icon.png'),
            require('../../assets/logo-text.png'),
        ]);

        const fontAssets = cacheFonts([
            { OpenSans: require('../../assets/fonts/OpenSans.ttf') },
            { OpenSansBold: require('../../assets/fonts/OpenSans-Bold.ttf') },
            {
                OpenSansSemiBold: require('../../assets/fonts/OpenSans-SemiBold.ttf'),
            },
            { Roboto: require('../../assets/fonts/Roboto.ttf') },
            { Roboto_medium: require('../../assets/fonts/Roboto-Medium.ttf') },
            { RobotoBold: require('../../assets/fonts/Roboto-Bold.ttf') },
            {
                ProximaNova: require('../../assets/fonts/ProximaNova-Light.otf'),
            },
        ]);

        await Promise.all([
            ...imageAssets,
            ...fontAssets,
            getIPAddress(),
            this.getInitialData(),
            this.getUserData(),
        ]);
        Promise.resolve();
    };

    onError = (err) => console.warn('Failed to load app', err);

    render() {
        const { error, initializing } = this.state;

        if (initializing) {
            return (
                <AppLoading
                    startAsync={this.loadAssetsAsync}
                    onFinish={this.postInitialization}
                    onError={this.onError}
                />
            );
        }

        return (
            <View style={styles.container}>
                {error && <Text style={styles.error}>{error}</Text>}
            </View>
        );
    }
}

function mapStateToProps(state) {
    return {
        user: state.user,
        misc: state.misc,
    };
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators(
        {
            getMeta: getMeta,
            checkSession: checkSessionAction,
            getUserLists: getUserListsAction,
        },
        dispatch,
    );
}

export default connect(mapStateToProps, mapDispatchToProps)(SplashScreen);

const styles = StyleSheet.create({
    container: {
        flex: 1,
        backgroundColor: 'white',
        alignItems: 'center',
        justifyContent: 'center',
    },
    error: {
        marginTop: spacing.big,
        color: colors.red,
        textAlign: 'center',
        fontWeight: 'bold',
        fontSize: fontSize.semiMedium,
    },
});
