import React from 'react';
import { FlatList, StyleSheet, View, Text, Platform } from 'react-native';
import fontSize from '../../constants/fontSize';
import colors from '../../constants/colors';
import spacing from '../../constants/spacing';
import TextButton from '../app/buttons/TextButton';
import ProductCardCarrousel from '../products/ProductCardCarrousel';
import MediaCardCarrousel from '../media/MediaCardCarrousel';
import { search } from '../../store/search';
import { useDispatch, useSelector } from 'react-redux';
import AuthorCard from '../author/AuthorCard';
import { withNavigation } from 'react-navigation';

const ContentCard = ({ navigation, list, onProductLike }) => {
    const dispatch = useDispatch();
    const data = useSelector((state) => state.Search);
    const lists = useSelector((state) => state.Lists);
    const allLists = lists ? Object.values(lists) : [];
    const products = allLists.filter((x) => x.type === 'products');

    const seeMore = () => {
        dispatch(
            search({
                params: {
                    ...data.last,
                    tags: list.tags,
                    type: list.type,
                    term: list.term ? list.term : '',
                    product: [],
                    author: [],
                    tagType: null,
                    tagName: null,
                    page: 0,
                },
                stack: true,
            }),
        );
        navigation.navigate('SearchMain', { stopLoading: true });
    };

    const renderItem = ({ item }) => {
        switch (list.type) {
            case 'authors':
                return <AuthorCard data={item} loadFilters tags={list.tags} />;
            case 'products':
                const inList = products.find((x) =>
                    x.items.find((y) => y._id === item._id),
                );
                return (
                    <ProductCardCarrousel
                        product={item}
                        inList={inList}
                        onProductLike={onProductLike}
                    />
                );
            case 'media':
                return <MediaCardCarrousel data={item} />;
        }
    };
    let isWeb;
    if (Platform.OS === 'web') {
        isWeb = true;
    } else {
        isWeb = false;
    }

    return !list.data.length ? null : (
        <View style={styles.container}>
            <View style={styles.content}>
                <Text style={styles.title}>{list.title}</Text>
            </View>
            <View
                style={{
                    backgroundColor:
                        list.type === 'authors'
                            ? colors.extraLightGray
                            : 'white',
                }}>
                <FlatList
                    listKey={list._id}
                    data={list.data}
                    keyExtractor={(item) => item._id}
                    renderItem={renderItem}
                    scrollEnabled
                    horizontal
                    showsHorizontalScrollIndicator={isWeb}
                    contentContainerStyle={{ paddingRight: 20 }}
                />
            </View>
            <View style={styles.content}>
                <TextButton
                    style={styles.button}
                    textStyle={styles.buttonText}
                    onPress={seeMore}
                    text={list.button}
                />
            </View>
        </View>
    );
};

export default withNavigation(ContentCard);

const styles = StyleSheet.create({
    container: {
        backgroundColor: 'white',
        paddingBottom: 10,
    },
    carrousel: {
        paddingRight: 0,
    },
    content: {
        padding: spacing.big,
        marginTop: 10,
        marginBottom: 15,
    },
    title: {
        fontFamily: 'OpenSansSemiBold',
        fontSize: fontSize.h5,
        color: colors.darkGray,
    },
    button: {
        height: 45,
        borderRadius: 5,
        flex: 1,
        borderWidth: 1,
        borderColor: colors.extraDarkGray,
        marginTop: 15,
    },
    buttonText: {
        color: colors.extraDarkGray,
        fontFamily: 'OpenSansSemiBold',
        fontSize: fontSize.medium,
    },
});
