import React, { useEffect, useState } from 'react';
import { Platform, View, Modal } from 'react-native';
import WebModal from 'modal-react-native-web';
// import Modal from 'react-native-modal';
import Styles from '../../app/other/Styles';

const RenderModal = ({ children, options }) => {
    if (Platform.OS === 'web') {
        return (
            <WebModal ariaHideApp={false} {...options} style={{ height: 20 }}>
                <View style={Styles.container}>
                    <View style={Styles.subContainer}>{children}</View>
                </View>
            </WebModal>
        );
    } else {
        return (
            <Modal {...options}>
                <View style={Styles.container}>
                    <View style={Styles.subContainer}>{children}</View>
                </View>
            </Modal>
        );
    }
};

export default RenderModal;
