import React, { useEffect, useState } from 'react';
import { Platform, StyleSheet, Text, View } from 'react-native';
import { withNavigation } from 'react-navigation';
import Constants from 'expo-constants';
import { useDispatch, useSelector } from 'react-redux';
import StickyHeaderView from '../components/app/views/StickyHeaderView';
import i18n from '../utils/i18n';
import Loading from '../components/app/views/Loading';
import AuthorHeader from '../components/author/AuthorHeader';
import fontSize from '../constants/fontSize';
import colors from '../constants/colors';
import { getAuthorInfo } from '../store/author';
import AuthorExtraInfo from '../components/author/AuthorExtraInfo';
import MediaCard from '../components/media/MediaCard';
import FiltersModal from '../components/search/FiltersModal';
import { search, initialParams } from '../store/search';
import ProductCard from '../components/products/ProductCard';
import constants from '../constants/constants';
import spacing from '../constants/spacing';
import ListAddFlow from '../components/lists/ListAddFlow';
import { addItemList, removeItemList } from '../store/lists';

const AuthorScreen = ({ navigation }) => {
    const dispatch = useDispatch();
    const author = navigation.getParam('author');
    const filter = navigation.getParam('filter');
    const fromRoute = navigation.getParam('fromRoute');
    const tags = navigation.getParam('tags');
    const data = useSelector((state) => state.AuthorViewSearch);
    const searchData = useSelector((state) =>
        fromRoute === 'SearchMain' ? state.Search : state.HomeSearch,
    );
    const authorInfo = useSelector((state) => state.AuthorInfo);
    const lists = useSelector((state) => state.Lists);
    const allLists = lists ? Object.values(lists) : [];
    const products = allLists.filter((x) => x.type === 'products');
    const [state, setState] = useState({ visible: false });

    useEffect(() => {
        dispatch(
            search({
                params: {
                    ...initialParams,
                    author: [author._id],
                    type: 'media',
                    term: filter ? searchData.last.term : '',
                    tags: tags || (filter ? searchData.last.tags : []),
                    page: 0,
                    items: [],
                },
                view: '_author',
            }),
        );
        dispatch(getAuthorInfo(author._id));
    }, []);

    const loadMore = () => {
        if (!data.isLast && !data.loading) {
            dispatch(
                search({
                    params: {
                        ...data,
                        page: data.page + 1,
                    },
                    view: '_author',
                }),
            );
        }
    };

    const empty = (
        <View style={styles.listEmptyMessage}>
            <Text
                style={{
                    ...styles.listEmptyMessageText,
                    ...styles.listEmptyMessage,
                }}>
                {i18n.t('emptyMedia')}
            </Text>
        </View>
    );

    const seeProductMedia = (product) => {
        const name = product.name + ' by ' + product.brand;
        dispatch(
            search({
                params: {
                    ...initialParams,
                    author: [author._id],
                    product: [product._id],
                    type: 'media',
                    tagName: name,
                    tagType: 'Product',
                },
                view: '_author',
            }),
        );
    };

    const onProductLike = (item) => {
        setState({ ...state, visible: true, item: item, type: 'products' });
    };

    const addToList = (listId, item) => {
        dispatch(addItemList(listId, item));
    };

    const removeFromList = (listId, item) => {
        dispatch(removeItemList(listId, item));
    };

    function getCaption(item) {
        const articles_count = item.count - item.videos;
        const videos =
            item.videos > 0
                ? `${item.videos} Youtube video${item.videos > 1 ? 's' : ''}`
                : '';
        const articles =
            articles_count > 0
                ? `${articles_count} Article${articles_count > 1 ? 's' : ''}`
                : '';
        const and = item.videos > 0 && articles_count > 0 ? ' & ' : '';
        const extra = data.term && data.term !== '' ? ' on ' + data.term : '';

        return { full: videos + and + articles + extra, extra: extra };
    }

    const renderProduct = ({ item }) => {
        const caption = getCaption(item);
        const inList = products.find((x) =>
            x.items.find((y) => y._id === item._id),
        );
        return (
            <>
                <View style={styles.productCard}>
                    <ProductCard
                        product={item}
                        caption={caption}
                        seeMedia={seeProductMedia}
                        inList={inList}
                        onLike={onProductLike}
                    />
                </View>
                <View
                    style={{ backgroundColor: colors.lightGray, padding: 2 }}
                />
            </>
        );
    };

    const renderMedia = ({ item }) => <MediaCard data={item} hideAuthor />;

    const renderItem = (input) => {
        switch (data.type) {
            case constants.MEDIA:
                return renderMedia(input);
            case constants.PRODUCTS:
                return renderProduct(input);
        }
    };

    return data &&
        data.author &&
        author._id === data.author[0] &&
        (!authorInfo || author._id === authorInfo._id) ? (
        <StickyHeaderView
            data={data.items}
            empty={empty}
            loadMore={loadMore}
            loading={data.loading}
            hideAuthor={true}
            renderItem={renderItem}
            resetScroll={data.time}
            page={data.page}>
            <View
                style={{
                    marginTop:
                        Constants.statusBarHeight +
                        (Platform.OS === 'ios' ? 25 : 5),
                }}>
                <AuthorHeader author={author}>
                    <AuthorExtraInfo author={authorInfo} />
                </AuthorHeader>
                {state.visible ? (
                    <ListAddFlow
                        visible={state.visible}
                        item={state.item}
                        type={state.type}
                        onClose={() => setState({ visible: false })}
                        onAdd={addToList}
                        onDelete={removeFromList}
                    />
                ) : null}
            </View>
        </StickyHeaderView>
    ) : (
        <View style={styles.refreshContainer}>
            <Loading />
        </View>
    );
};

AuthorScreen.navigationOptions = ({ navigation }) => {
    const fromRoute = navigation.getParam('fromRoute');
    const tags = navigation.getParam('tags');
    return {
        headerTransparent: true,
        title: null,
        headerRight: () => (
            <View style={{ paddingRight: 20 }}>
                <FiltersModal
                    loadFilters={navigation.getParam('filter')}
                    view={'_author'}
                    fromRoute={fromRoute}
                    tags={tags}
                />
            </View>
        ),
    };
};

export default withNavigation(AuthorScreen);

const styles = StyleSheet.create({
    refreshContainer: {
        flex: 1,
        alignItems: 'center',
        justifyContent: 'center',
        backgroundColor: 'white',
    },
    listEmptyMessage: {
        flex: 1,
        alignItems: 'center',
        backgroundColor: 'white',
        paddingTop: 10,
    },
    listEmptyMessageText: {
        fontSize: fontSize.medium,
        color: colors.darkGray,
    },
    productCard: {
        backgroundColor: 'white',
        paddingTop: spacing.big,
        paddingLeft: spacing.medium,
        paddingRight: spacing.medium,
        marginTop: 0,
        marginBottom: 4,
        marginLeft: 0,
        marginRight: 0,
        paddingBottom: spacing.big,
    },
});
